import { Box } from '@mui/material'
import React from 'react'
import styled from '@emotion/styled'
import Title from '@/components/head/Title'
import Tools from '@/components/head/Tools'
import SearchBar from '@/components/head/SearchBar'
import Container from '@/components/Container'
import MainNav from '@/components/head/MainNav'

const StyledDiv = styled.div`
    display: flex;
    height: 296px;
    background-color: #BEE2F5;
    opacity: 1;
`

type Props = {}

const Header = (props: Props) => {
    return (
        <StyledDiv>
            <Container sx={{
                height: 1,
                backgroundImage: 'url("/images/header.png")',
                backgroundRepeat: "no-repeat",
                backgroundPosition: "right 255px top 8px",
                backgroundSize: "auto 280px",
                display: "flex",
                flexDirection: "column"
            }}>
                <Box display="flex" paddingTop="20px" alignItems="center">
                    <Title></Title>
                    <Tools></Tools>
                </Box>
                <Box width="577px" paddingTop="50px">
                    <SearchBar></SearchBar>
                </Box>
                <MainNav></MainNav>
            </Container>
        </StyledDiv>
    )
}

export default Header