import { getHotSaleProducts } from "@/api/product.api"
import { useInfiniteQuery } from "react-query"

const SIZE = 4

/** 获取热卖商品(按评论总数、收藏总数排序) */
const useScrollHotSaleProducts = () => {
    return useInfiniteQuery(
        ['scroll_hot_products'],
        ({ pageParam = 1 }) => {
            return getHotSaleProducts(pageParam, SIZE)
        }, {
        getNextPageParam: (lastPage) => {
            if (lastPage.next) {
                const url = new URL(lastPage.next)
                const params = new URLSearchParams(url.search)
                const nextPage = parseInt(params.get('page')!)
                return nextPage
            }
        }
    })
}
export default useScrollHotSaleProducts