import React from 'react'
import { Box, IconButton, InputAdornment, TextField, TextFieldProps } from '@mui/material'
import { Visibility, VisibilityOff } from '@mui/icons-material'

interface IFormikConfig {
    values: any,
    handleChange: any,
    handleBlur: any,
    touched: any,
    errors: any
    autoFocus?: boolean
}

type Props = {
    formik: IFormikConfig,
    name: string
}

const PasswordInput = ({ formik, value, onChange, onBlur, error, helperText, name, variant = "outlined", autoFocus = false, ...rest }: Props & TextFieldProps) => {
    const [showPassword, setShowPassword] = React.useState(false)
    const handleClick = () => {
        setShowPassword(!showPassword)
    }
    return (
        <Box marginTop={2} width={1}>
            <TextField
                fullWidth
                autoFocus={autoFocus}
                name={name}
                variant={variant}
                value={formik.values[name]}
                onChange={formik.handleChange}
                error={formik.touched[name] && Boolean(formik.errors[name])}
                helperText={formik.touched[name] && formik.errors[name]}
                type={showPassword ? "text" : "password"}
                InputProps={{
                    readOnly: showPassword,
                    endAdornment: <InputAdornment position='end'>
                        <IconButton onClick={handleClick}>
                            {showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                    </InputAdornment>
                }}
                {...rest}
            />
        </Box>
    )
}

export default PasswordInput