import React from 'react'
import { Alert, AlertColor, Slide, Snackbar, SnackbarCloseReason } from '@mui/material'

export interface IToastMessageProps {
    message: string,
    color: AlertColor,
    open: boolean,
    setOpen: React.Dispatch<React.SetStateAction<boolean>>
}

function SlideTransition(props: any) {
    return <Slide {...props} direction="down" />;
}

const ToastMessage = (props: IToastMessageProps) => {
    const handleClose = (event: React.SyntheticEvent<any> | Event, reason: SnackbarCloseReason): void => {
        if (reason === 'clickaway') {
            return;
        }
        props.setOpen(false)
    }

    return (
        <Snackbar
            open={props.open}
            autoHideDuration={3000}
            anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            onClose={handleClose}
            TransitionComponent={SlideTransition}
        >
            <Alert severity={props.color} variant="filled">{props.message}</Alert>
        </Snackbar>
    )
}

export default ToastMessage