import useToast from "@/hooks/useToast"
import IAddComment from "@/interfaces/IAddComment"
import IBaseComment from "@/interfaces/IBaseComment"
import { useMutation, useQueryClient } from "react-query"

interface IArgs {
    queryKey: string
    action: (request: IAddComment) => Promise<IBaseComment>
}

const useAddComment = ({ queryKey, action }: IArgs) => {
    const { showSuccess } = useToast()
    const client = useQueryClient()
    return useMutation<IBaseComment, Error, IAddComment>(action, {
        onSuccess: () => {
            client.invalidateQueries([queryKey])
            showSuccess("操作成功")
        }
    })
}
export default useAddComment