import React from 'react'
import styled from '@emotion/styled'
import RemoveButton from '@/components/RemoveButton'

type Props = {
    url: string,
    onRmove: (url?: string) => void
}

const StyledDiv = styled.div`
    background-color: #F5F5F5;
    width: 88px;
    height: 88px;
    position: relative;
    margin-left: 24px;
    & img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
`

const ImageBox = (props: Props) => {
    const handleClick = () => {
        props.onRmove(props.url)
    }
    return (
        <StyledDiv>
            {props.url && <img src={props.url} alt="image"></img>}
            <RemoveButton onClick={handleClick}></RemoveButton>
        </StyledDiv>
    )
}

export default ImageBox