import React from 'react'
import { Box, TextField, TextFieldProps } from '@mui/material'
import { styled } from '@mui/system'

interface IFormikConfig {
    values: any,
    handleChange: any,
    handleBlur: any,
    touched: any,
    errors: any
    autoFocus?: boolean
}

type Props = {
    formik: IFormikConfig,
    name: string
}

/** 文本输入框（集成表单验证） */
const TextInput = ({ formik, value, onChange, onBlur, error, helperText, name, variant = "outlined", autoFocus = false, ...rest }: Props & TextFieldProps) => {
    return (
        <Box marginTop={2} width={1}>
            <TextField
                fullWidth
                autoFocus={autoFocus}
                name={name}
                variant={variant}
                value={formik.values[name]}
                onChange={formik.handleChange}
                error={formik.touched[name] && Boolean(formik.errors[name])}
                helperText={formik.touched[name] && formik.errors[name]}
                {...rest}
            />
        </Box>
    )
}

export default TextInput