import IImage from "@/interfaces/IImage";
import IProductForm from "./IProductForm";
import { readAuthInfo } from "@/utils/storage";
import produce from "immer"
import { PaymentMethods } from "@/enums";


export interface IProductFormState extends IProductForm {
}

const getShopId = () => {
    const auth = readAuthInfo()
    if (auth) {
        return auth.shop_id
    } else {
        return ''
    }
}

export const defaultProductFormState: IProductFormState = {
    shop: getShopId(),
    name: '',
    detail: '',
    country: '',
    state: '',
    city: '',
    address1: '',
    address2: '',
    latitude: 0,
    longitude: 0,
    payment_methods: [],
    is_second_hand: false,
    status: '1',
    images: [],
    price: 0
}

export type ProductFormAction =
    | { type: 'init', payload: IProductFormState }
    | { type: 'reset' }
    | { type: 'changeName', payload: string }
    | { type: 'changeDetail', payload: string }
    | { type: 'changeAddress', payload: string }
    | { type: 'changePaymentMethods', payload: { value: PaymentMethods, isChecked: boolean } }
    | { type: 'changeImages', payload: IImage[] }
    | { type: 'changeIsSecondHand', payload: boolean }
    | { type: 'changePrice', payload: number }

export type ProductFormDispatch = (action: ProductFormAction) => void

export const ProductFormReducer = (state: IProductFormState, action: ProductFormAction): IProductFormState => {
    switch (action.type) {
        case 'init':
            return action.payload
        case "reset":
            return defaultProductFormState
        case "changeName":
            return produce(state, draft => {
                draft.name = action.payload
            })
        case "changeDetail":
            return produce(state, draft => {
                draft.detail = action.payload
            })
        case 'changeAddress':
            return produce(state, draft => {
                draft.address1 = action.payload
            })
        case 'changeImages':
            return produce(state, draft => {
                draft.images = action.payload
            })
        case 'changePaymentMethods':
            return produce(state, draft => {
                let set = new Set(state.payment_methods)
                if (action.payload.isChecked) {
                    set.add(action.payload.value)
                } else {
                    set.delete(action.payload.value)
                }
                draft.payment_methods = Array.from(set)
            })
        case 'changeIsSecondHand':
            return produce(state, draft => {
                draft.is_second_hand = action.payload
            })
        case 'changePrice':
            return produce(state, draft => {
                draft.price = action.payload
            })
        default:
            throw new Error('Unhandled action type');
    }
}

