import React from 'react'
import { Box, Button, Checkbox as MUICheckbox, FormControlLabel, InputAdornment } from '@mui/material'

type Props = {
    label: string,
    value: string,
    checked?: boolean,
    onChange: (event: React.ChangeEvent<HTMLInputElement>, checked: boolean) => void
}

const Checkbox = (props: Props) => {
    return (
        <FormControlLabel
            control={
                <MUICheckbox name="checkbox" size='small' checked={props.checked} onChange={props.onChange} value={props.value}/>
            }
            label={
                <Box component="div" fontSize={14}>
                    {props.label}
                </Box>
            }
        />
    )
}

export default Checkbox