import React from 'react'
import styled from '@emotion/styled'
import { Box } from '@mui/system'
import { ShopIndustry, ShopIndustryRecord } from '@/enums'
import classNames from 'classnames';
import { useSearchParams } from 'react-router-dom';


const StyledTitle = styled.p`
    color: #333333;
    font-size: 1.125rem;
    font-weight: bold;    
`

const StyledLink = styled.a`
    font-size: 14px;
    color: #333333;
    text-decoration: none;
    transition: linear 0.2s;
    padding: 5px;
    display: block;
    &:hover{
        text-decoration: underline;
    }
   &.active{
    font-weight: bold;
   }
`

type Props = {
}

/** 行业过滤UI组件 */
const IndustryFilter = (props: Props) => {
    const [searchParams, setSearchParams] = useSearchParams();
    const [value, setValue] = React.useState<ShopIndustry | undefined>(searchParams.get('industry') as ShopIndustry)

    const handleClick = (e: React.MouseEvent, newValue: ShopIndustry) => {
        e.preventDefault()
        setValue(newValue)
    }

    React.useEffect(()=>{
        const newValue = searchParams.get('industry') as ShopIndustry
        setValue(newValue)
    },[searchParams])

    React.useEffect(() => {
        let newParams = new URLSearchParams(searchParams)
        if (value) {
            newParams.set('industry', value)
            setSearchParams(newParams)
        } else {
            newParams.delete('industry')
            setSearchParams(newParams)
        }
    }, [value])

    return (
        <Box>
            <StyledTitle>行业</StyledTitle>
            <StyledLink
                href='#'
                className={classNames({ active: value === ShopIndustry.retail })}
                onClick={e => handleClick(e, ShopIndustry.retail)}
            >
                {ShopIndustryRecord[ShopIndustry.retail]}
            </StyledLink>
            <StyledLink
                href='#'
                onClick={e => handleClick(e, ShopIndustry.wholesale)}
                className={classNames({ active: value === ShopIndustry.wholesale })}
            >
                {ShopIndustryRecord[ShopIndustry.wholesale]}
            </StyledLink>
            <StyledLink
                href='#'
                className={classNames({ active: value === ShopIndustry.clothing })}
                onClick={e => handleClick(e, ShopIndustry.clothing)}
            >
                {ShopIndustryRecord[ShopIndustry.clothing]}
            </StyledLink>
            <StyledLink
                href='#'
                className={classNames({ active: value === ShopIndustry.food })}
                onClick={e => handleClick(e, ShopIndustry.food)}
            >
                {ShopIndustryRecord[ShopIndustry.food]}
            </StyledLink>
            <StyledLink
                href='#'
                className={classNames({ active: value === ShopIndustry.hotel })}
                onClick={e => handleClick(e, ShopIndustry.hotel)}
            >
                {ShopIndustryRecord[ShopIndustry.hotel]}
            </StyledLink>
            <StyledLink
                href='#'
                className={classNames({ active: value === ShopIndustry.transportation })}
                onClick={e => handleClick(e, ShopIndustry.transportation)}
            >
                {ShopIndustryRecord[ShopIndustry.transportation]}
            </StyledLink>
            <StyledLink
                href='#'
                className={classNames({ active: value === ShopIndustry.electronic })}
                onClick={e => handleClick(e, ShopIndustry.electronic)}
            >
                {ShopIndustryRecord[ShopIndustry.electronic]}
            </StyledLink>
            <StyledLink
                href='#'
                className={classNames({ active: value === ShopIndustry.DigitalGoods })}
                onClick={e => handleClick(e, ShopIndustry.DigitalGoods)}
            >
                {ShopIndustryRecord[ShopIndustry.DigitalGoods]}
            </StyledLink>
        </Box>
    )
}

export default IndustryFilter