import React from 'react'
import styled from '@emotion/styled'
import { Box } from '@mui/material'
import SvgIcon from '@mui/material/SvgIcon'

const StyledP = styled.p`
    font-size: 1.875rem;
    color: #333333;
    margin-left: 13px;
`
const Icon = () => {
    return (
        <svg xmlns="http://www.w3.org/2000/svg" width="33" height="27" viewBox="0 0 33 27">
            <path id="Icon_material-featured-play-list" data-name="Icon material-featured-play-list" d="M31.5,4.5H4.5a3.009,3.009,0,0,0-3,3v21a3.009,3.009,0,0,0,3,3h27a3.009,3.009,0,0,0,3-3V7.5A3.009,3.009,0,0,0,31.5,4.5ZM18,16.5H4.5v-3H18Zm0-6H4.5v-3H18Z" transform="translate(-1.5 -4.5)" fill="#53aa09" />
        </svg>
    )
}
const MarkerIcon = () => {
    return (
        <SvgIcon component={Icon}></SvgIcon>
    )
}

/** 推荐商品标题 */
const SuggestProductsTitle = () => {
    return (
        <Box sx={{ display: 'flex', alignItems: 'center', paddingBottom: 2 }}>
            <MarkerIcon></MarkerIcon>
            <StyledP>为您推荐</StyledP>
        </Box>
    )
}

export default SuggestProductsTitle