import React from 'react'
import * as yup from 'yup'
import { useFormik } from 'formik'
import TextInput from '@/components/TextInput'
import PasswordInput from '@/components/PasswordInput'
import { LoadingButton } from '@mui/lab'
import Paths from '@/layout/Paths'
import { Link as RouterLink, useNavigate } from "react-router-dom"
import { Alert, Box, Link } from '@mui/material'
import useAuth from '@/hooks/useAuth'
import useLogin from '@/query/account/useLogin'

type Props = {}

const Login = (props: Props) => {
  const action = useLogin()
  const { dispatch } = useAuth()
  const navigate = useNavigate()
  const validationSchema = yup.object({
    email: yup.string().trim().required("不能是一个空字符串").matches(/^[^\s@]+@[^\s@]+\.[^\s@]+$/, "EMAIL格式不正确"),
    password: yup.string().trim().required("不能是一个空字符串"),
  })
  const formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    validationSchema: validationSchema,
    onSubmit: async (values, { resetForm }) => {
      let password = values.password.trim()
      let email = values.email.trim().toLowerCase()
      let response = await action.mutateAsync({ email, password })
      dispatch({ type: 'login', payload: response })
      resetForm()
      navigate('/')
    }
  })
  return (
    <Box marginX="auto" bgcolor="#F7F7F7" width="580px" marginY="60px" paddingY="60px" paddingX="80px">
      <p style={{ color: "#333333", fontSize: "30px", lineHeight: "45px" }}>登录</p>
      <form onSubmit={formik.handleSubmit} autoComplete="off">
        <TextInput
          label="Email"
          name='email'
          formik={formik}
          sx={{ bgcolor: "white" }}
        />
        <Box paddingBottom='50px'>
          <PasswordInput
            label="密码"
            name='password'
            formik={formik}
            sx={{ bgcolor: "white" }}
          />
        </Box>
        {action.isError && (
          <Alert severity="error" sx={{ marginBottom: 1 }}>{action.error.message}</Alert>
        )}
        <LoadingButton
          loading={action.isLoading}
          size="large"
          type="submit"
          variant="contained"
          fullWidth
        >登录</LoadingButton>
        <p style={{ fontSize: "14px", lineHeight: "28px", paddingTop: "20px" }}>没有帐户？
          <Link underline="hover" component={RouterLink} to={Paths.ACCOUNT_REGISTER}>现在注册</Link>。
        </p>
      </form>
    </Box>
  )
}

export default Login