import { getPopularShops } from "@/api/shop.api"
import { useInfiniteQuery } from "react-query"

const SIZE = 4

/** 获取热门商家的滚动数据 */
const useScrollPopularShops = () => {
    return useInfiniteQuery(
        ['scroll_popular_shops'],
        ({ pageParam = 1 }) => {
            return getPopularShops(pageParam, SIZE)
        }, {
        getNextPageParam: (lastPage) => {
            if (lastPage.next) {
                const url = new URL(lastPage.next)
                const params = new URLSearchParams(url.search)
                const nextPage = parseInt(params.get('page')!)
                return nextPage
            }
        }
    })
}
export default useScrollPopularShops