import ErrorMessage from '@/components/ErrorMessage'
import FieldTitle from '@/components/FieldTitle'
import ImageUploadGroup from '@/components/ImageUploadGroup'
import TextField from '@/components/TextField'
import { ShopIndustry, ShopStatus } from '@/enums'
import useToast from '@/hooks/useToast'
import useDefaultShop from '@/query/shop/useDefaultShop'
import useUpdateShop from '@/query/shop/useUpdateShop'
import { LoadingButton } from '@mui/lab'
import { Alert, Box, MenuItem, Select, SelectChangeEvent, Skeleton } from '@mui/material'
import React from 'react'
import { ShopReducer, defaultShopState } from './shop.store'

const Loading = () => {
    return (
        <React.Fragment>
            <Skeleton variant="text" width={650} />
            <Skeleton variant="rectangular" width={80} height={80} />
            <Skeleton variant="text" width={300} />
            <Skeleton variant="text" width={300} />
        </React.Fragment>
    )
}

type Props = {}

/** 店铺设置 */
const Shop = (props: Props) => {
    const [state, dispatch] = React.useReducer(ShopReducer, defaultShopState)
    const { showSuccess } = useToast()
    const query = useDefaultShop()
    const action = useUpdateShop()

    React.useEffect(() => {
        if (query.isSuccess) {
            dispatch({ type: 'init', payload: query.data! })
        }
    }, [query.isSuccess, query.data])

    React.useEffect(() => {
        if (action.isSuccess) {
            showSuccess("操作成功")
        }
    }, [action.isSuccess])

    const handleAddressChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = e.target.value.trim()
        dispatch({
            type: "changeAddress", payload: value
        })
    }
    const handleIndustryChange = (event: SelectChangeEvent) => {
        let value = event.target.value as ShopIndustry
        dispatch({ type: "changeIndustry", payload: value })
    }
    const handleStatusChange = (event: SelectChangeEvent) => {
        let value = event.target.value as ShopStatus
        dispatch({ type: "changeStatus", payload: value })
    }

    const handleSubmit = (e: React.FormEvent) => {
        e.stopPropagation()
        e.preventDefault()
        action.mutate(state)
    }
    if (query.isLoading || query.isIdle) {
        return <Loading></Loading>
    }
    if (query.isError) {
        return <Alert severity="error">{query.error.message}</Alert>
    }
    return (
        <Box width="650px">
            <form onSubmit={handleSubmit}>
                <FieldTitle first>店铺名称</FieldTitle>
                <TextField maxLength={30} fullWidth value={state.name} onChange={e => dispatch({ type: 'changeName', payload: e.target.value })}></TextField>
                <FieldTitle>店铺图片</FieldTitle>
                <ImageUploadGroup
                    disable={state.images.length >= 4}
                    value={state.images}
                    onChange={images => dispatch({ type: 'changeImages', payload: images })}></ImageUploadGroup>
                <div style={{ width: "312px" }}>
                    <FieldTitle>地址</FieldTitle>
                    <TextField maxLength={30} value={state.address1} onChange={handleAddressChange} fullWidth></TextField>
                    <FieldTitle>联系电话</FieldTitle>
                    <TextField value={state.phone_num} fullWidth onChange={e => dispatch({ type: 'changePhoneNum', payload: e.target.value })}></TextField>
                    <FieldTitle>营业时间</FieldTitle>
                    <Box display='flex' alignItems='center'>
                        <TextField
                            value={state.business_hours[0].start_time}
                            sx={{ flex: 1 }}
                            maxLength={5}
                            onChange={e => dispatch({ type: 'changeStartTime', payload: e.target.value })}
                        ></TextField>
                        <span style={{ paddingLeft: '8px', paddingRight: '8px' }}>至</span>
                        <TextField
                            value={state.business_hours[0].end_time}
                            sx={{ flex: 1 }}
                            maxLength={5}
                            onChange={e => dispatch({ type: 'changeEndTime', payload: e.target.value })}
                        ></TextField>
                    </Box>
                    <FieldTitle>行业</FieldTitle>
                    <Select value={state.industry} fullWidth onChange={handleIndustryChange}>
                        <MenuItem value={ShopIndustry.wholesale}>批发</MenuItem>
                        <MenuItem value={ShopIndustry.retail}>零售</MenuItem>
                        <MenuItem value={ShopIndustry.clothing}>服装</MenuItem>
                        <MenuItem value={ShopIndustry.food}>食品</MenuItem>
                        <MenuItem value={ShopIndustry.hotel}>酒店</MenuItem>
                        <MenuItem value={ShopIndustry.transportation}>交通</MenuItem>
                        <MenuItem value={ShopIndustry.electronic}>消费电子</MenuItem>
                        <MenuItem value={ShopIndustry.DigitalGoods}>数字商品</MenuItem>
                    </Select>
                    <FieldTitle>状态</FieldTitle>
                    <Select value={state.status} onChange={handleStatusChange} fullWidth>
                        <MenuItem value={ShopStatus.Opening}>正常营业</MenuItem>
                        <MenuItem value={ShopStatus.Suspension}>暂停营业</MenuItem>
                    </Select>
                </div>
                <FieldTitle>店铺详情</FieldTitle>
                <TextField multiline rows={4} fullWidth value={state.detail} onChange={e => dispatch({ type: 'changeDetail', payload: e.target.value })}></TextField>
                {action.isError && <ErrorMessage message={action.error.message}></ErrorMessage>}
                <LoadingButton loading={action.isLoading} type='submit' variant="contained" size='small' sx={{ marginTop: 1 }}>发布</LoadingButton>
            </form>
        </Box>
    )
}

export default Shop