import React from 'react'
import * as yup from 'yup'
import { useFormik } from 'formik'
import TextInput from '@/components/TextInput'
import PasswordInput from '@/components/PasswordInput'
import { LoadingButton } from '@mui/lab'
import Paths from '@/layout/Paths'
import { Link as RouterLink, useNavigate } from "react-router-dom"
import { Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, FormControlLabel, Link, Radio, RadioGroup, Alert } from '@mui/material'
import { AccountType } from '@/enums'
import useRegister from '@/query/account/useRegister'

type Props = {}

const Register = (props: Props) => {
    const action = useRegister()
    const navigate = useNavigate()
    const [type, setType] = React.useState<AccountType>(AccountType.Individual)
    const validationSchema = yup.object({
        username: yup.string().trim().required("不能是一个空字符串").matches(/^[0-9a-zA-Z]+$/, "只能输入字母、数字").min(3, "至少需要3个字符"),
        password: yup.string().trim().required("不能是一个空字符串").matches(/^[0-9a-zA-Z]+$/, "只能输入字母、数字").min(6, "至少需要6个字符"),
        // 正则表达式来源：https://stackoverflow.com/a/9204568
        email: yup.string().trim().required("不能是一个空字符串").matches(/^[^\s@]+@[^\s@]+\.[^\s@]+$/, "EMAIL格式不正确")
    })
    const handleClick = () => {
        navigate(Paths.ACCOUNT_LOGIN)
    }
    const handleTypeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        let value = (event.target as HTMLInputElement).value as AccountType
        setType(value)
    }

    const formik = useFormik({
        initialValues: {
            username: "",
            password: "",
            email: ""
        },
        validationSchema: validationSchema,
        onSubmit: (values) => {
            let username = values.username.trim().toLowerCase()
            let password = values.password.trim()
            let email = values.email.trim().toLowerCase()
            action.mutate({
                username,
                password,
                email,
                account_type: type
            })
        }
    })
    return (
        <Box marginX="auto" bgcolor="#F7F7F7" width="580px" marginY="60px" paddingY="60px" paddingX="80px">
            <form onSubmit={formik.handleSubmit} autoComplete="off">
                <p style={{ color: "#333333", fontSize: "30px", lineHeight: "45px" }}>注册</p>
                <TextInput name='username' label="用户名" formik={formik} sx={{ bgcolor: "white" }}></TextInput>
                <TextInput name='email' label="Email" formik={formik} sx={{ bgcolor: "white" }}></TextInput>
                <PasswordInput name='password' label="密码" formik={formik} sx={{ bgcolor: "white" }}></PasswordInput>
                <Box paddingY={2}>
                    <RadioGroup
                        value={type}
                        onChange={handleTypeChange}
                        row={true}
                    >
                        <FormControlLabel value={AccountType.Business} control={<Radio />} label="商家" />
                        <FormControlLabel value={AccountType.Individual} control={<Radio />} label="个人" />
                    </RadioGroup>
                </Box>
                {action.isError && <Alert severity="error" sx={{ marginBottom: 1 }}>{action.error.message}</Alert>}
                <LoadingButton
                    loading={action.isLoading}
                    size="large"
                    type="submit"
                    variant="contained"
                    fullWidth
                >
                    创建账户
                </LoadingButton>
                <p style={{ paddingTop: "20px", fontSize: "14px" }}>已经有一个帐户？ 在这里<Link underline="hover" component={RouterLink} to={Paths.ACCOUNT_LOGIN}>登录</Link>。</p>
            </form>
            <Dialog open={action.isSuccess} sx={{
                '& .MuiDialogContent-root': {
                    padding: "8px",
                    width: "500px"
                },
                '& .MuiDialogActions-root': {
                    padding: "8px",
                },
            }}>
                <DialogTitle>提示</DialogTitle>
                <DialogContent>
                    <DialogContentText sx={{ paddingLeft: 2 }}>
                        注册成功
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClick}>现在登录</Button>
                </DialogActions>
            </Dialog>
        </Box>
    )
}

export default Register