import React from 'react'
import Alert from '@mui/material/Alert'
import { Box } from '@mui/material'

type Props = {
    message?: string
}

const ErrorMessage = (props: Props) => {
    if (props.message) {
        return (
            <Box sx={{ paddingY: 1 }}>
                <Alert severity="error">{props.message}</Alert>
            </Box>
        )
    } else {
        return <></>
    }
}

export default ErrorMessage