import { useQuery } from "react-query"
import { getShopById } from "@/api/shop.api"
import IShopView from "@/interfaces/IShopView"
import useAuth from "@/hooks/useAuth"
import useMyAccount from "../account/useMyAccount"
import useShop from "./useShop"

/** 获取登录账号的默认店铺 */
const useDefaultShop = () => {
    const auth = useAuth()
    return useShop(auth.state.shopId!)
}
export default useDefaultShop