import IndustryFilter from '@/filters/IndustryFilter'
import { Box, Grid } from '@mui/material'
import React from 'react'
import Data from './core/Data'
import PriceFilter from './core/PriceFilter'
import SecondHandFilter from './core/SecondHandFilter'

type Props = {}

/** 商品列表页面 */
const Products = (props: Props) => {
    return (
        <Grid container>
            <Grid item xs={2}>
            <Box paddingY={2}>
                    <IndustryFilter></IndustryFilter>
                </Box>
                <Box paddingY={2}>
                    <SecondHandFilter></SecondHandFilter>
                </Box>
                <Box paddingBottom={2}>
                    <PriceFilter></PriceFilter>
                </Box>
            </Grid>
            <Grid item xs={10}>
                <Data></Data>
            </Grid>
        </Grid>
    )
}

export default Products