import ShopCard, { ShopCardSkeleton } from '@/components/ShopCard';
import { RatingNumType, ShopIndustry, ShopStatus } from '@/enums';
import FilterResult from '@/filters/FilterResult';
import PageFilter from '@/filters/PageFilter';
import IShopsFilter from '@/interfaces/IShopsFilter';
import useSearchShops from '@/query/shop/useSearchShops';
import { generateArray } from '@/utils/array.helper';
import { Alert, Box, Grid } from '@mui/material';
import React from 'react'
import { useSearchParams } from 'react-router-dom';

type Props = {}

const SIZE = 20

const getInitOption = (params: URLSearchParams) => {
    let pageParam = params.get('page')
    let industryParam = params.get('industry')
    let ratingParam = params.get('rating')
    let statusParam = params.get('status')
    let result: IShopsFilter = {
        page: pageParam ? parseInt(pageParam) : 1,
        size: SIZE
    }
    if (industryParam) {
        result.industry = industryParam as ShopIndustry
    }
    if (ratingParam) {
        result.rating = ratingParam as RatingNumType
    }
    if (statusParam) {
        result.status = statusParam as ShopStatus
    } else {
        result.status = ShopStatus.Opening
    }
    return result
}

const Loading = () => {
    const items = generateArray(8)
    return (
        <Box paddingTop={3}>
            <Grid container spacing={2}>
                {items.map(item => (
                    <Grid item key={item} xs={3}>
                        <ShopCardSkeleton></ShopCardSkeleton>
                    </Grid>
                ))}
            </Grid>
        </Box>
    )
}

/** 店铺查询数据 */
const Data = (props: Props) => {
    const [searchParams, setSearchParams] = useSearchParams()
    const [option, setOption] = React.useState<IShopsFilter>(getInitOption(searchParams))
    const query = useSearchShops(option)

    React.useEffect(() => {
        setOption(getInitOption(searchParams))
    }, [searchParams])

    if (query.isLoading) {
        return <Loading></Loading>
    }

    if (query.data) {
        return (
            <Box paddingY={2}>
                <Box paddingBottom={3}>
                    <FilterResult count={query.data.count}></FilterResult>
                </Box>
                {(query.data.results && query.data.count > 0) && (
                    <React.Fragment>
                        <Grid container spacing={2}>
                            {query.data.results.map(item => (
                                <Grid item xs={3} key={item.id}>
                                    <ShopCard shop={item}></ShopCard>
                                </Grid>
                            ))}
                        </Grid>
                        <Box paddingTop={2}>
                            <PageFilter size={SIZE} count={query.data.count}></PageFilter>
                        </Box>
                    </React.Fragment>
                )}
                {query.data.count === 0 && <Alert severity="warning">暂无数据</Alert>}
            </Box>
        )
    } else {
        return <></>
    }
}

export default Data