import React from 'react'
import { createContext, useState } from 'react'
import { AlertColor } from '@mui/material/Alert'
import ToastMessage from '@/components/ToastMessage'

interface Props {
    children: React.ReactNode
}

export interface IMessageContext {
    message: string,
    setMessage?: React.Dispatch<React.SetStateAction<string>>,
    color?: AlertColor,
    setColor?: React.Dispatch<React.SetStateAction<AlertColor>>,
    open?: boolean,
    setOpen?: React.Dispatch<React.SetStateAction<boolean>>
}

export const MessageContext = createContext<IMessageContext>({
    message: '',
    color: 'error',
    open: false,
})

export const ToastProvider = (props: Props) => {
    const [message, setMessage] = useState('')
    const [color, setColor] = useState<AlertColor>('error')
    const [open, setOpen] = useState<boolean>(false)
    return (
        <div>
            <MessageContext.Provider value={{ message, setMessage, color, setColor, open, setOpen }}>
                <ToastMessage message={message} color={color} open={open} setOpen={setOpen}></ToastMessage>
                {props.children}
            </MessageContext.Provider>
        </div>
    )
}

const useToast = () => {
    const context = React.useContext(MessageContext)
    const show = (msg: string, color: AlertColor) => {
        context.setMessage?.(msg)
        context.setColor?.(color)
        context.setOpen?.(true)
    }
    const showError = (msg: string) => { show(msg, 'error') }
    const showInfo = (msg: string) => { show(msg, 'info') }
    const showSuccess = (msg: string) => { show(msg, 'success') }
    const showWarning = (msg: string) => { show(msg, 'warning') }
    return { showError, showInfo, showSuccess, showWarning }
}
export default useToast