import IGeocode from "@/interfaces/IGeocode";
import ILocation from "@/interfaces/ILocation";
import IPoint from "@/interfaces/IPoint";
import { isNullOrWhitespace } from "@/utils/string.helper";

/**
 * 解析谷歌API的查询结果
 * @param result 谷歌解析结果
 * @param address 用户输入的地址
 */
const parseGoogleResponse = (result: google.maps.GeocoderResult, address: string): ILocation => {
    let countryShortName = ''
    let state = ''
    let city = ''
    let components = result.address_components
    if (components) {
        // 获取国家短代码（例如：CN）
        let countryItem = components.find(value => {
            return value.types.includes('country')
        })
        if (countryItem) {
            countryShortName = countryItem['short_name']
        }
        // 获取州名或省名
        let stateItem = components.find(value => value.types.includes('administrative_area_level_1'))
        if (stateItem) {
            state = stateItem['long_name']
        }
        // 获取城市名
        let cityItem = components.find(value => {
            return value.types.includes('locality') && value.types.includes('political')
        })
        if (cityItem) {
            city = cityItem['long_name']
        }
    }
    return {
        country: countryShortName,
        state: state,
        city: city,
        latitude: result.geometry.location.lat(),
        longitude: result.geometry.location.lng(),
        input_address: address,
        formatted_address: result.formatted_address
    }
}

/** 通过地址获取地理位置（经纬度、国家、城市），若字符串为空，则返回默认值 */
export const getLocationFromAddress = (address: string): Promise<ILocation> => {
    if (isNullOrWhitespace(address)) {
        return new Promise((resolve, reject) => {
            resolve({
                country: '',
                state: '',
                city: '',
                latitude: 0,
                longitude: 0,
                input_address: '',
                formatted_address: ''
            })
        })
    } else {
        var geocoder = new google.maps.Geocoder();
        return new Promise((resolve, reject) => {
            geocoder.geocode({ address }, function (results, status) {
                switch (status) {
                    case google.maps.GeocoderStatus.OK:
                        resolve(parseGoogleResponse(results![0], address))
                        break;
                    case google.maps.GeocoderStatus.ZERO_RESULTS:
                        reject(new Error('该地址不存在或不合法'))
                        break;
                    case google.maps.GeocoderStatus.OVER_QUERY_LIMIT:
                        reject(new Error('API调用超出配额'))
                        break
                    case google.maps.GeocoderStatus.INVALID_REQUEST:
                        reject(new Error('缺少部分查询信息'))
                        break
                    case google.maps.GeocoderStatus.UNKNOWN_ERROR:
                        reject(new Error('无法处理请求,请重试'))
                        break
                    case google.maps.GeocoderStatus.ERROR:
                        reject(new Error('请求超时,请重试'))
                        break
                    default:
                        reject(new Error('未知处理异常'))
                        break;
                }
            })
        })
    }


}