import ErrorMessage from '@/components/ErrorMessage'
import useToast from '@/hooks/useToast'
import useCreateProduct from '@/query/product/useCreateProduct'
import { LoadingButton } from '@mui/lab'
import React from 'react'
import { ProductFormReducer, defaultProductFormState } from './form/product.form.store'
import ProductForm from './form/ProductForm'

type Props = {}

/** 创建商品 */
const CreateProduct = (props: Props) => {
    const { showSuccess } = useToast()
    const mutation = useCreateProduct()
    const [state, dispatch] = React.useReducer(ProductFormReducer, defaultProductFormState)

    React.useEffect(() => {
        if (mutation.isSuccess) {
            showSuccess("操作成功")
            dispatch({ type: 'reset' })
        }
    }, [mutation.isSuccess])

    const handleSubmit = (e: React.FormEvent) => {
        e.stopPropagation()
        e.preventDefault()
        mutation.mutate(state)
    }

    return (
        <div style={{ width: "650px" }}>
            <ProductForm state={state} dispatch={dispatch} onSubmit={handleSubmit}>
                {mutation.isError && (
                    <ErrorMessage message={mutation.error.message}></ErrorMessage>
                )}
                <LoadingButton loading={mutation.isLoading} type='submit' variant="contained" size='small' sx={{ marginTop: 1 }}>发布</LoadingButton>
            </ProductForm>
        </div>
    )
}

export default CreateProduct