import React from 'react'
import styled from '@emotion/styled'
import { checkImageType } from '@/utils/image'
import useToast from '@/hooks/useToast'
import { default as MuiAddIcon } from '@mui/icons-material/Add'
import { Icon } from '@mui/material'
import CircularProgress from '@mui/material/CircularProgress';
import { uploadImage } from '@/api/upload.api'
import IImage from '@/interfaces/IImage'

type Props = {
    onUpload: (image: IImage) => void
    disable?: boolean
}

const StyledDiv = styled.div`
    width: 88px;
    height: 88px;
    background-color: #F5F5F5;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    cursor: pointer;
    color: #000000;
    transition: background-color ease 0.2s;
    &:hover{
        background-color: rgba(245,245,245,0.8);
    }
    &.disable{
        pointer-events:none;
        color: #e0e0e0;
    }
`

const AddIcon = () => {
    return (
        <Icon>
            <MuiAddIcon></MuiAddIcon>
        </Icon>
    )
}

const UploadButton = (props: Props) => {
    const ref = React.useRef<HTMLInputElement>(null)
    const { showError } = useToast()
    const [loading, setLoading] = React.useState(false)

    const handleInputChange = () => {
        let input = ref.current
        if (input) {
            if (input.files) {
                let file = input.files[0]
                if (file) {
                    if (checkImageType(file)) {
                        setLoading(true)
                        uploadImage(file).then(result => {
                            props.onUpload(result)
                        }).catch(err => {
                            showError("上传文件失败")
                        }).finally(() => {
                            setLoading(false)
                        })
                    } else {
                        showError("只能上传图片文件")
                    }
                }
            }
        }
    }
    const handleInputClick = () => {
        let input = ref.current
        if (input) {
            input.value = ""
        }
    }
    const handleClick = () => {
        let input = ref.current
        if (input) {
            input.click()
        }
    }

    return (
        <StyledDiv onClick={handleClick} className={`${props.disable ? "disable" : ""}`}>
            {loading ? <CircularProgress></CircularProgress> : <span>+</span>}
            <input
                type="file"
                ref={ref}
                accept="image/jpg, image/jpeg, image/gif, image/png"
                style={{ display: "none" }}
                onChange={handleInputChange}
                onClick={handleInputClick}
            />
        </StyledDiv >
    )
}

export default UploadButton