import { ShopStatus } from '@/enums'
import React from 'react'
import styled from '@emotion/styled'
import { useSearchParams } from 'react-router-dom'
import { FormControlLabel, Radio, RadioGroup, Typography } from '@mui/material'

const StyledTitle = styled.p`
    color: #333333;
    font-size: 1.125rem;
    font-weight: bold;    
`

const StyledBox = styled.div`
    & .MuiRadio-root{
        padding-top: 3px;
        padding-bottom: 3px;
        display: flex;
        align-items: center;
    }
    & .MuiTypography-root{
        display: flex;
        align-items: center;
    }
`

type Props = {}

const convertValue = (params: URLSearchParams): ShopStatus => {
    if (params.has('status')) {
        /** 如果有值，则解析值 */
        const value = params.get('status')
        if (value) {
            return value as ShopStatus
        } else {
            return ShopStatus.Opening
        }
    } else {
        // 如果没有值，则默认为正常营业
        return ShopStatus.Opening
    }
}

/** 店铺营业状态过滤器 */
const StatusFilter = (props: Props) => {
    const [searchParams, setSearchParams] = useSearchParams()
    const [value, setValue] = React.useState<ShopStatus>(convertValue(searchParams))

    // 观察URL的变化，并修改状态
    React.useEffect(() => {
        let newValue = convertValue(searchParams)
        setValue(newValue)
    }, [searchParams])

    // 观察状态的变化，并设置URL
    React.useEffect(() => {
        let newParams = new URLSearchParams(searchParams)
        if (value === ShopStatus.Opening) {
            newParams.set('status', ShopStatus.Opening)
        } else if (value === ShopStatus.Suspension) {
            newParams.set('status', ShopStatus.Suspension)
        } else {
            newParams.set('status', ShopStatus.All)
        }
        setSearchParams(newParams)
    }, [value])

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        let newValue = (event.target as HTMLInputElement).value
        setValue(newValue as ShopStatus);
    }
    return (
        <StyledBox>
            <StyledTitle>营业状态</StyledTitle>
            <RadioGroup value={value} onChange={handleChange}>
                <FormControlLabel
                    value={ShopStatus.All}
                    control={<Radio size="small" value={ShopStatus.All} />}
                    label={<Typography style={{ fontSize: '1rem' }}>All</Typography>}
                />
                <FormControlLabel
                    value={ShopStatus.Opening}
                    control={<Radio size="small" value={ShopStatus.Opening} />}
                    label={<Typography style={{ fontSize: '1rem' }}>正常营业</Typography>}
                />
                <FormControlLabel
                    value={ShopStatus.Suspension}
                    control={<Radio size="small" value={ShopStatus.Suspension} />}
                    label={<Typography style={{ fontSize: '1rem' }}>暂停营业</Typography>}
                />
            </RadioGroup>
        </StyledBox>
    )
}

export default StatusFilter