import IAddComment from '@/interfaces/IAddComment'
import IBaseComment from '@/interfaces/IBaseComment'
import IGetCommentsFunc from '@/interfaces/IGetCommentsFunc'
import IPagingResult from '@/interfaces/IPagingResult'
import useAddComment from '@/query/comment/useAddComment'
import { Alert, Box, Pagination, Skeleton } from '@mui/material'
import React from 'react'
import { useQuery, UseQueryResult } from 'react-query'
import Comment from './Comment'
import Editor from './Editor'

type Props = {
    targetId: string
    insert: (args: IAddComment) => Promise<IBaseComment>
    queryKey: string,
    query: IGetCommentsFunc
}

const size = 10

interface IDataProps {
    data: IPagingResult<IBaseComment>
    page: number
    onChange: (page: number) => void
}

const Data = ({ data, page, onChange }: IDataProps) => {
    const handleChange = (event: React.ChangeEvent<unknown>, newPage: number) => {
        onChange(newPage)
    }

    if (data.count > 0) {
        const total = Math.ceil(data.count / size)
        return (
            <React.Fragment>
                {data.results.map(item => (
                    <Comment data={item} key={item.id}></Comment>
                ))}
                {total > 0 && (
                    <Box paddingY={1}>
                        <Pagination count={total} page={page} shape="rounded" onChange={handleChange} />
                    </Box>
                )}
            </React.Fragment>
        )
    } else {
        return <Alert severity="success">暂无评论</Alert>
    }
}

/** 评论组件，包含评论发表，列表渲染 */
const CommentBox = (props: Props) => {
    const [value, setValue] = React.useState('')
    const [page, setPage] = React.useState(1)
    const insertAction = useAddComment({
        queryKey: props.queryKey,
        action: props.insert
    })
    const query = useQuery<IPagingResult<IBaseComment>, Error>(
        [props.queryKey, { targetId: props.targetId, page: page }],
        () => props.query(props.targetId, page, size),
    )

    React.useEffect(() => {
        // 提交成功时，清空编辑框
        if (insertAction.isSuccess) {
            setValue('')
        }
    }, [insertAction.isSuccess])

    const handleChange = (newValue: string) => {
        setValue(newValue)
    }
    const handleClick = () => {
        insertAction.mutate({
            targetId: props.targetId,
            body: value
        })
    }

    // 当点击分页按钮时，刷新分页数据
    const handleChangePage = (newPage: number) => {
        setPage(newPage)
    }
    if (query.isLoading) {
        return (
            <Box paddingX={1}>
                <Skeleton />
                <Skeleton />
                <Skeleton />
            </Box>
        )
    }
    if (query.isError) {
        return <Alert severity="error">{query.error.message}</Alert>
    }
    return (
        <Box paddingX={1}>
            <Editor
                value={value}
                onChange={handleChange}
                onClick={handleClick}
                isSaving={insertAction.isLoading}
            />
            {query.data && <Data data={query.data} page={page} onChange={handleChangePage}></Data>}
        </Box>
    )
}

export default CommentBox