import React from 'react'
import styled from '@emotion/styled'
import HeartOutlineIcon from '@/icons/HeartOutlineIcon'
import HeartIcon from '@/icons/HeartIcon'


const StyledDefault = styled.div`
    height: 2rem;
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    background-color: #FFFFFF;
    border: 1px #C3C3C3 solid;
    color: #FF4500;
    padding: 8px 16px;
    border-radius: 1rem;
    min-width: 110px;
    user-select: none;
`

const StyledDisable = styled.div`
    height: 2rem;
    display: inline-flex;
    align-items: center;
    cursor: default;
    background-color: #FFFFFF;
    border: 1px #C3C3C3 solid;
    color: #FF4500;
    padding: 8px 16px;
    border-radius: 1rem;
    min-width: 110px;
    user-select: none;
`

const StyledText = styled.p`
    color: #000000;
    font-size: 14px;
    padding-left: 8px;
`

interface IButtonProps {
    onClick?: () => void
}

const DefaultButton = (props: IButtonProps) => {
    const handleClick = () => {
        if (props.onClick) {
            props.onClick()
        }
    }
    return (
        <StyledDefault onClick={handleClick}>
            <HeartOutlineIcon sx={{ fontSize: '20px' }}></HeartOutlineIcon>
            <StyledText>收藏</StyledText>
        </StyledDefault>
    )
}

const DisableButton = () => {
    return (
        <StyledDisable >
            <HeartIcon sx={{ fontSize: '20px' }}></HeartIcon>
            <StyledText>已收藏</StyledText>
        </StyledDisable>
    )
}

type Props = {
    isFav: boolean,
    onClick?: () => void
}

const FavoriteButton = (props: Props) => {
    return (
        <React.Fragment>
            {props.isFav ? <DisableButton></DisableButton> : <DefaultButton onClick={props.onClick}></DefaultButton>}
        </React.Fragment>
    )
}

export default FavoriteButton