import ProductCard, { ProductCardSkeleton } from '@/components/ProductCard'
import { PriceNumType, ShopIndustry } from '@/enums'
import FilterResult from '@/filters/FilterResult'
import PageFilter from '@/filters/PageFilter'
import IProductsFilter from '@/interfaces/IProductsFilter'
import useSearchProducts from '@/query/product/useSearchProducts'
import { generateArray } from '@/utils/array.helper'
import { Alert, Box, Grid } from '@mui/material'
import React from 'react'
import { useSearchParams } from 'react-router-dom'

const SIZE = 20

const Loading = () => {
    const items = generateArray(8)
    return (
        <Box paddingTop={3}>
            <Grid container spacing={2}>
                {items.map(item => (
                    <Grid item key={item} xs={3}>
                        <ProductCardSkeleton></ProductCardSkeleton>
                    </Grid>
                ))}
            </Grid>
        </Box>
    )
}

const convertSecondHandValue = (isSecondHandParam: string | null) => {
    if (isSecondHandParam == null) {
        return null
    } else if (isSecondHandParam === 'false') {
        return false
    } else {
        return true
    }
}

const getInitOption = (params: URLSearchParams) => {
    let pageParam = params.get('page')
    let priceParam = params.get('price')
    let isSecondHandParam = params.get('is_second_hand')
    let industryParam = params.get('industry')
    let result: IProductsFilter = {
        page: pageParam ? parseInt(pageParam) : 1,
        size: SIZE,
        is_second_hand: convertSecondHandValue(isSecondHandParam),
        price: priceParam == null ? PriceNumType.a : priceParam as PriceNumType
    }
    if (industryParam) {
        result.industry = industryParam as ShopIndustry
    }
    return result
}

/** 产品列表 */
const Data = () => {
    const [searchParams, setSearchParams] = useSearchParams()
    const [option, setOption] = React.useState<IProductsFilter>(getInitOption(searchParams))
    const query = useSearchProducts(option)
    React.useEffect(() => {
        setOption(getInitOption(searchParams))
    }, [searchParams])

    if (query.isLoading) {
        return <Loading></Loading>
    }

    if (query.data) {
        return (
            <Box paddingY={2}>
                <Box paddingBottom={3}>
                    <FilterResult count={query.data.count}></FilterResult>
                </Box>
                {(query.data.results && query.data.count > 0) && (
                    <React.Fragment>
                        <Grid container spacing={2}>
                            {query.data.results.map(item => (
                                <Grid item xs={3} key={item.id}>
                                    <ProductCard data={item}></ProductCard>
                                </Grid>
                            ))}
                        </Grid>
                        <Box paddingTop={2}>
                            <PageFilter size={SIZE} count={query.data.count}></PageFilter>
                        </Box>
                    </React.Fragment>
                )}
                {query.data.count === 0 && <Alert severity="warning">暂无数据</Alert>}
            </Box>
        )
    } else {
        return <></>
    }
}

export default Data