import SeeMore from '@/components/SeeMore'
import { Alert, Box, Grid, SxProps, Theme } from '@mui/material'
import React from 'react'
import ShopCard from '@/components/ShopCard'
import PopularShopsTitle from './PopularShopsTitle'
import LoadingSkeleton from './LoadingSkeleton'
import useScrollPopularShops from '@/query/shop/useScrollPopularShops'
import IPagingResult from '@/interfaces/IPagingResult'
import IShopView from '@/interfaces/IShopView'

interface IRenderProps {
    data?: IPagingResult<IShopView>[]
}
const Render = ({ data }: IRenderProps) => {
    if (data) {
        return (
            <React.Fragment>
                {data.map(page => {
                    return page.results.map(item => (
                        <Grid item xs={3} flex={1} key={item.id}>
                            <ShopCard shop={item}></ShopCard>
                        </Grid>
                    ))
                })}
            </React.Fragment>
        )
    } else {
        return <></>
    }
}

type Props = {
    sx?: SxProps<Theme>
}

/** 热门商家 */
const PopularShops = ({ sx }: Props) => {
    const query = useScrollPopularShops()
    if (query.isLoading) {
        return <LoadingSkeleton></LoadingSkeleton>
    } else if (query.isError) {
        return <Alert severity="error">{(query.error as Error).message}</Alert>
    } else {
        return (
            <React.Fragment>
                <Box sx={sx}>
                    <PopularShopsTitle></PopularShopsTitle>
                    <Grid container spacing={2}>
                        <Render data={query.data?.pages}></Render>
                    </Grid>
                    <SeeMore onClick={() => query.fetchNextPage()} hide={!query.hasNextPage} isLoading={query.isFetchingNextPage}></SeeMore>
                </Box>
            </React.Fragment>
        )
    }
}

export default PopularShops