import { ShopCardSkeleton } from '@/components/ShopCard'
import { Grid, SxProps, Theme } from '@mui/material'
import React from 'react'


type Props = {
    sx?: SxProps<Theme>
}

/** 首页骨架屏 */
const LoadingSkeleton = ({ sx }: Props) => {
    return (
        <Grid container spacing={2} sx={sx}>
            <Grid item xs={3}>
                <ShopCardSkeleton></ShopCardSkeleton>
            </Grid>
            <Grid item xs={3}>
                <ShopCardSkeleton></ShopCardSkeleton>
            </Grid>
            <Grid item xs={3}>
                <ShopCardSkeleton></ShopCardSkeleton>
            </Grid>
            <Grid item xs={3}>
                <ShopCardSkeleton></ShopCardSkeleton>
            </Grid>
        </Grid>
    )
}

export default LoadingSkeleton