import { RatingNumType } from '@/enums'
import { FormControlLabel, Radio, RadioGroup, Rating, Typography } from '@mui/material'
import React from 'react'
import styled from '@emotion/styled'
import { useSearchParams } from 'react-router-dom'

const StyledBox = styled.div`
    & .MuiRadio-root{
        padding-top: 3px;
        padding-bottom: 3px;
        display: flex;
        align-items: center;
    }
    & .MuiTypography-root{
        display: flex;
        align-items: center;
    }
`

const StyledTitle = styled.p`
    color: #333333;
    font-size: 1.125rem;
    font-weight: bold;    
`

const convertValue = (params: URLSearchParams) => {
    return params.has('rating') ? params.get('rating') as RatingNumType : RatingNumType.all
}

type Props = {}

/** 评分过滤器 */
const RatingFilter = (props: Props) => {
    const [searchParams, setSearchParams] = useSearchParams()
    const [value, setValue] = React.useState<RatingNumType>(convertValue(searchParams))

    // 观察URL的变化，并设置状态
    React.useEffect(() => {
        const newValue = convertValue(searchParams)
        setValue(newValue)
    }, [searchParams])

    // 观察状态的变化，并设置URL
    React.useEffect(() => {
        let newParams = new URLSearchParams(searchParams)
        if (value) {
            if (value == RatingNumType.all) {
                newParams.delete('rating')
            } else {
                newParams.set('rating', value)
            }
        } else {
            newParams.delete('rating')
        }
        setSearchParams(newParams)
    }, [value])

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        let newValue = (event.target as HTMLInputElement).value
        setValue(newValue as RatingNumType);
    }

    return (
        <StyledBox>
            <StyledTitle>评分</StyledTitle>
            <RadioGroup value={value} onChange={handleChange}>
                <FormControlLabel
                    value={RatingNumType.all}
                    control={<Radio size="small" />}
                    label={<Typography style={{ fontSize: '1rem' }}>All</Typography>}
                />
                <FormControlLabel
                    value={RatingNumType.five}
                    control={<Radio size="small" />}
                    label={<Rating value={5} size="small" readOnly />}
                />
                <FormControlLabel
                    value={RatingNumType.four}
                    control={<Radio size="small" />}
                    label={<Rating value={4} size="small" readOnly />}
                />
                <FormControlLabel
                    value={RatingNumType.three}
                    control={<Radio size="small" />}
                    label={<Rating value={3} size="small" readOnly />}
                />
                <FormControlLabel
                    value={RatingNumType.two}
                    control={<Radio size="small" />}
                    label={<Rating value={2} size="small" readOnly />}
                />
                <FormControlLabel
                    value={RatingNumType.one}
                    control={<Radio size="small" />}
                    label={<Rating value={1} size="small" readOnly />}
                />
            </RadioGroup>
        </StyledBox>
    )
}

export default RatingFilter