import React from 'react'
import styled from '@emotion/styled'
import { Box, Grid } from '@mui/material'
import RegisterTimeIcon from '@/icons/RegisterTimeIcon'
import TimeIcon from '@/icons/TimeIcon'
import PhoneIcon from '@/icons/PhoneIcon'
import MessageIcon from '@/icons/MessageIcon'
import ShopIcon from '@/icons/ShopIcon'
import PaymentIcon from '@/icons/PaymentIcon'
import IShopView from '@/interfaces/IShopView'
import { getDateString } from '@/utils/string.helper'
import { parseBusinessHour } from '@/api/shop.api'

const StyledText = styled.p`
    color: #333333;
    font-size: 14px;
    padding-left: 0.5rem;
`

type Props = {
    shop: IShopView
}

interface IItemProps {
    children: React.ReactNode
}
const Item = (props: IItemProps) => {
    return (
        <Grid item xs={6} paddingY={1.5}>
            <Box display='flex' alignItems='center'>
                {props.children}
            </Box>
        </Grid>
    )
}

const Info = ({shop}: Props) => {
    return (
        <Grid container paddingY={1}>
            <Item>
                <RegisterTimeIcon></RegisterTimeIcon>
                <StyledText>{getDateString(shop.created_time)}</StyledText>
            </Item>
            <Item>
                <TimeIcon></TimeIcon>
                <StyledText>{parseBusinessHour(shop.business_hours[0])}</StyledText>
            </Item>
            <Item>
                <PhoneIcon></PhoneIcon>
                <StyledText>{shop.phone_num}</StyledText>
            </Item>
            <Item>
                <MessageIcon></MessageIcon>
                <StyledText>在线联系</StyledText>
            </Item>
            <Item>
                <ShopIcon></ShopIcon>
                <StyledText>在售商品 {shop.spu_count}</StyledText>
            </Item>
            <Item>
                <PaymentIcon></PaymentIcon>
                <StyledText>HPay</StyledText>
            </Item>
        </Grid>
    )
}

export default Info