import { getRecommendProducts } from "@/api/product.api"
import { useInfiniteQuery } from "react-query"

const SIZE = 4

/** 获取推荐商品的滚动数据 */
const useScrollSuggestProducts = () => {
    return useInfiniteQuery(
        ['scroll_suggest_products'],
        ({ pageParam = 1 }) => {
            return getRecommendProducts(pageParam, SIZE)
        }, {
        getNextPageParam: (lastPage) => {
            if (lastPage.next) {
                const url = new URL(lastPage.next)
                const params = new URLSearchParams(url.search)
                const nextPage = parseInt(params.get('page')!)
                return nextPage
            }
        }
    })
}
export default useScrollSuggestProducts