import React from 'react'

type Props = {
    first?: boolean,
    children: React.ReactNode
}

const FieldTitle = (props: Props) => {
    return (
        <p style={{
            color: "#333333",
            fontSize: "18px",
            lineHeight: "25px",
            paddingBottom: "10px",
            paddingTop: props.first ? "0" : "30px"
        }}>
            {props.children}
        </p>
    )
}

export default FieldTitle