import { Pagination } from '@mui/material'
import { Box } from '@mui/system'
import React from 'react'
import { useSearchParams } from 'react-router-dom'

type Props = {
    size: number,
    count: number
}

const convertValue = (params: URLSearchParams) => {
    let page = params.get('page')
    if (page) {
        return parseInt(page)
    } else {
        return 1
    }
}

/** 分页过滤器 */
const PageFilter = ({ size, count }: Props) => {
    const [searchParams, setSearchParams] = useSearchParams()
    const [value, setValue] = React.useState(convertValue(searchParams))
    const totalPage = Math.ceil(count / size)

    React.useEffect(() => {
        const newValue = convertValue(searchParams)
        setValue(newValue)
    }, [searchParams])

    React.useEffect(() => {
        let newParams = new URLSearchParams(searchParams)
        if (value < 1) {
            newParams.set('page', '1')
        } else {
            newParams.set('page', value.toString())
        }
        setSearchParams(newParams)
    }, [value])

    const handleChange = (event: React.ChangeEvent<unknown>, page: number) => {
        setValue(page)
    }

    return (
        <Box>
            {totalPage > 0 && <Pagination count={totalPage} page={value} onChange={handleChange}></Pagination>}
        </Box>
    )
}

export default PageFilter