import { Box } from '@mui/system'
import React from 'react'
import styled from '@emotion/styled'
import ProgressBar from '@/components/ProgressBar'

const Label = styled.p`
    font-size: 0.875rem;
    padding-right: 1rem;    
    color: #333333;
`

const Text = styled.p`
    font-size: 0.875rem;
    color: #333333;
    width: 2.5rem;
    padding-left: 1rem;
`

type Props = {
    label: string,
    max: number,
    value: number
}

/** 带标签的进度条 */
const ProgressWithLabel = (props: Props) => {
    let text = props.max === 0 ? 0 : (props.value / props.max)
    text = Math.round(text * 100)
    return (
        <Box display='flex' alignItems='center' gap={1} paddingTop={1}>
            <Label>{props.label}</Label>
            <ProgressBar max={props.max} value={props.value}></ProgressBar>
            <Text>{text + '%'}</Text>
        </Box>
    )
}

export default ProgressWithLabel