import ErrorMessage from '@/components/ErrorMessage';
import IProduct from '@/interfaces/IProduct';
import useModifyProduct from '@/query/product/useModifyProduct'
import useProductById from '@/query/product/useProductById';
import { LoadingButton } from '@mui/lab';
import { Alert, Skeleton } from '@mui/material';
import React from 'react'
import { useParams } from 'react-router-dom';
import { defaultProductFormState, ProductFormReducer } from './form/product.form.store';
import ProductForm from './form/ProductForm';

const Loading = () => {
  return (
    <React.Fragment>
      <Skeleton variant="text" width={650} />
      <Skeleton variant="rectangular" width={80} height={80} />
      <Skeleton variant="text" width={300} />
      <Skeleton variant="text" width={300} />
    </React.Fragment>
  )
}

type Props = {}

/** 修改商品 */
const ModifyProduct = (props: Props) => {
  let { id } = useParams()
  const query = useProductById(id!)
  const mutation = useModifyProduct()
  const [state, dispatch] = React.useReducer(ProductFormReducer, defaultProductFormState)

  const handleSubmit = (e: React.FormEvent) => {
    e.stopPropagation()
    e.preventDefault()
    let product: IProduct = { ...state, id: id! }
    mutation.mutate(product)
  }

  React.useEffect(() => {
    if (query.data && query.isSuccess) {
      dispatch({ type: 'init', payload: query.data })
    }
  }, [query.data, query.isSuccess])


  if (query.isLoading) {
    return <Loading></Loading>
  }
  if (query.data) {
    return (
      <div style={{ width: "650px" }}>
        <ProductForm state={state} dispatch={dispatch} onSubmit={handleSubmit}>
          {mutation.isError && (
            <ErrorMessage message={mutation.error.message}></ErrorMessage>
          )}
          <LoadingButton loading={mutation.isLoading} type='submit' variant="contained" size='small' sx={{ marginTop: 1 }}>保存</LoadingButton>
        </ProductForm>
      </div>
    )
  } else {
    return <Alert severity="warning">该商品不存在或已被删除</Alert>
  }
}

export default ModifyProduct