import React from 'react'
import styled from '@emotion/styled'
import CloseIcon from '@mui/icons-material/Close'
import { Icon } from '@mui/material'

const StyledDiv = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    background-color: #E31111;
    color: white;
    border-radius: 10px;
    transition: background-color ease 0.2s;
    &:hover{
        background-color: rgba(277,17,17,0.8);
    }
    font-size: 12px;
    position: absolute;
    right: -10px;
    top: -10px;
    cursor: pointer;
`

type Props = {
    onClick?: () => void
}

const RemoveButton = (props: Props) => {
    return (
        <StyledDiv onClick={props.onClick}>
            <Icon sx={{ transform: 'scale(0.5) translateX(5%) translateY(-10%)' }}>
                <CloseIcon></CloseIcon>
            </Icon>
        </StyledDiv>
    )
}

export default RemoveButton