import { REACT_APP_API_URL } from "@/config"
import IJsonResult from "@/interfaces/IJsonResult"
import IRefreshTokenResponse from "@/interfaces/IRefreshTokenResponse"
import axios from "axios"

/** 返回 access token*/
export const refreshToken = async (refresh: string): Promise<string> => {
    const url = `${REACT_APP_API_URL}/login/refresh`
    const response = await axios.post(url, { refresh })
    const data = response.data as IJsonResult<IRefreshTokenResponse>
    if (data.success) {
        return data.content.access
    } else {
        throw new Error(data.message)
    }
}