import SidebarBoxTitle from '@/components/title/SidebarBoxTitle'
import IProductView from '@/interfaces/IProductView'
import React from 'react'
import styled from '@emotion/styled'
import { Box, Grid } from '@mui/material'
import MapMarkerIcon from '@/icons/MapMarkerIcon'
import RegisterTimeIcon from '@/icons/RegisterTimeIcon'
import { getDateString } from '@/utils/string.helper'
import IShopView from '@/interfaces/IShopView'
import PaymentIcon from '@/icons/PaymentIcon'
import ShopIcon from '@/icons/ShopIcon'
import useHotSaleProducts from '@/query/product/useHotSaleProducts'
import ProductCard from '@/components/ProductCard'

type Props = {
    product: IProductView
    shop: IShopView
}

const StyledName = styled.p`
    color: #0A82FF;
    font-size: 20px;
    padding-right: 11px;
`

const StyledText = styled.p`
    font-size: 14px;
    line-height: 14px;
    color: #333333;
    padding-left: 18px;
`

/** 推荐商品 */
const List = () => {
    const query = useHotSaleProducts(1, 4)
    if (query.data) {
        return (
            <React.Fragment>
                <SidebarBoxTitle sx={{ paddingY: 2 }}>推荐商品</SidebarBoxTitle>
                <Grid container spacing={2}>
                    {query.data.results.map(item => (
                        <Grid item xs={6} key={item.id}>
                            <ProductCard data={item}></ProductCard>
                        </Grid>
                    ))}
                </Grid>
            </React.Fragment>
        )
    } else {
        return <></>
    }
}

/** 卖家信息 */
const SellerBox = ({ product, shop }: Props) => {
    const query = useHotSaleProducts(1, 4)
    return (
        <div>
            <SidebarBoxTitle sx={{ paddingY: 2 }}>卖家信息</SidebarBoxTitle>
            <Box display='flex' alignItems="center" sx={{ paddingBottom: 2 }}>
                <StyledName>{product.user.username}</StyledName>
                <img src='/icons/user_verified_Icon.png'></img>
            </Box>
            <Box display='flex' alignItems="center" sx={{ paddingBottom: 2 }}>
                <MapMarkerIcon></MapMarkerIcon>
                <StyledText>{product.address2}</StyledText>
            </Box>
            <Box display='flex' alignItems="center" sx={{ paddingBottom: 2 }}>
                <RegisterTimeIcon></RegisterTimeIcon>
                <StyledText>
                    注册时间：{getDateString(shop.created_time)}
                </StyledText>
                <p style={{ width: '60px' }}></p>
                <PaymentIcon></PaymentIcon>
                <StyledText>HPay</StyledText>
            </Box>
            {/* TODO 后端缺乏API */}
            <Box display='flex' alignItems="center" sx={{ paddingBottom: 2 }}>
                <ShopIcon></ShopIcon>
                <StyledText>在售商品 12</StyledText>
            </Box>
            <List></List>
        </div>
    )
}

export default SellerBox