import React from 'react'
import styled from '@emotion/styled'
import IShopLocation from '@/interfaces/IShopLocation'
import { GoogleMap, InfoWindow, Marker } from "@react-google-maps/api";
import { Box, Rating } from '@mui/material';

const StyledImage = styled.img`
    width: 100%;
    object-fit: cover;
`

type Props = {
  data: IShopLocation
}

const StyledDiv = styled.div`
  width: 100%;
  aspect-ratio: 10 / 12;
`

const StyledName = styled.p`
  font-size: 1rem;
  color: black;  
  overflow : hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
`

const StyledRartingCount = styled.p`
  color  : #2680EB;
  font-size: 14px;  
  line-height: 14px;
  padding-left: 5px;
`

interface IShopWindowProps {
  data: IShopLocation
}
const ShopWindow = ({ data }: IShopWindowProps) => {
  return (
    <Box
      display="grid"
      gridTemplateColumns='1fr 1fr'
      gridTemplateRows='auto 1fr'
      gridTemplateAreas="'img title' 'img rating'"
      rowGap={1}
      columnGap={1}
    >
      <Box gridArea='img'>
        <StyledImage src={data.images[0].image}></StyledImage>
      </Box>
      <StyledName style={{ gridArea: 'title' }}>{data.name}</StyledName>
      <Box gridArea='rating' display='flex' alignItems='center'>
        <Rating
          name="size-small"
          defaultValue={0}
          value={data.rating}
          readOnly
          size="small"
        />
        <StyledRartingCount>{data.rating_count}</StyledRartingCount>
      </Box>
    </Box>
  )
}


/** 店铺谷歌地图 */
const ShopMap = ({ data }: Props) => {
  const domRef = React.useRef<HTMLDivElement | null>(null)
  const [isMounted, setIsMounted] = React.useState(false)
  const [showInfo, setShowInfo] = React.useState(false)
  React.useEffect(() => setIsMounted(true), [])
  const positon = { lat: data.latitude, lng: data.longitude }

  const handleClick = () => {
    setShowInfo(true)
  }

  const handleCloseClick = () => {
    setShowInfo(false)
  }

  return (
    <StyledDiv ref={domRef}>
      <GoogleMap
        mapContainerStyle={{ width: "100%", height: "100%" }}
        center={positon}
        zoom={10}
      >
        {isMounted && (
          <Marker position={positon} onClick={handleClick}>
            {showInfo && (
              <InfoWindow onCloseClick={handleCloseClick}>
                <ShopWindow data={data}></ShopWindow>
              </InfoWindow>
            )}
          </Marker>
        )}
      </GoogleMap>
    </StyledDiv>
  )
}

export default ShopMap