import { REACT_APP_API_URL } from "@/config"
import { ShopIndustry, ShopStatus } from "@/enums"
import IAddComment from "@/interfaces/IAddComment"
import IBusinessHour from "@/interfaces/IBusinessHour"
import IGetComments from "@/interfaces/IGetComments"
import IGetCommentsFunc from "@/interfaces/IGetCommentsFunc"
import IJsonResult from "@/interfaces/IJsonResult"
import IPagingResult from "@/interfaces/IPagingResult"
import IShop from "@/interfaces/IShop"
import IShopComment from "@/interfaces/IShopComment"
import IShopFilter from "@/interfaces/IShopFilter"
import IShopsFilter from "@/interfaces/IShopsFilter"
import IShopView from "@/interfaces/IShopView"
import { http } from "@/utils/http"
import { readAuthInfo } from "@/utils/storage"
import { isNullOrWhitespace } from "@/utils/string.helper"
import { getMyAccount } from "./account.api"
import { getLocationFromAddress } from "./google.api"

/** 获取登录账号的默认Shop */
export const getDefaultShop = async (): Promise<IShop | null> => {
    const user = readAuthInfo()
    if (user) {
        const account = await getMyAccount(user.accessToken)
        const url = `${REACT_APP_API_URL}/shops/${account.shop}`
        const response = await http.get(url)
        const data = response.data as IJsonResult<IShop>
        if (!data.success) {
            throw new Error(data.message)
        }
        // 赋默认值，默认店铺的一些属性可能为undefined,不方便表单验证
        if (isNullOrWhitespace(data.content.name)) {
            data.content.name = ''
        }
        if (isNullOrWhitespace(data.content.phone_num)) {
            data.content.phone_num = ''
        }
        if (isNullOrWhitespace(data.content.industry)) {
            data.content.industry = ShopIndustry.retail
        }
        if (isNullOrWhitespace(data.content.status)) {
            data.content.status = ShopStatus.Opening
        }
        if (data.content.business_hours === undefined || data.content.business_hours.length === 0) {
            data.content.business_hours = [{
                start_time: '09:00',
                end_time: '15:00'
            }]
        }
        return data.content
    } else {
        return null
    }
}

/** 更新店铺资料 */
export const updateShop = async (newShop: IShop) => {
    if (isNullOrWhitespace(newShop.address1)) {
        throw new Error('地址不能为空')
    }
    // 通过谷歌API获取店铺位置
    let location = await getLocationFromAddress(newShop.address1)
    const clone = JSON.parse(JSON.stringify(newShop)) as IShop
    clone.latitude = location.latitude
    clone.longitude = location.longitude
    clone.country = location.country
    clone.city = location.city
    clone.state = location.state
    clone.address2 = location.formatted_address
    const url = `${REACT_APP_API_URL}/shops/${newShop.id}`
    const response = await http.patch(url, clone)
    const data = response.data as IJsonResult<IShop>
    if (!data.success) {
        console.error(data.message)
        throw new Error(data.message)
    }
}

/** 通过ID获取店铺 */
export const getShopById = async (id: string) => {
    const url = `${REACT_APP_API_URL}/shops/${id}`
    const response = await http.get(url)
    const data = response.data as IJsonResult<IShopView>
    // 赋默认值，默认店铺的一些属性可能为undefined,不方便表单验证
    if (isNullOrWhitespace(data.content.name)) {
        data.content.name = ''
    }
    if (isNullOrWhitespace(data.content.phone_num)) {
        data.content.phone_num = ''
    }
    if (isNullOrWhitespace(data.content.industry)) {
        data.content.industry = ShopIndustry.retail
    }
    if (isNullOrWhitespace(data.content.status)) {
        data.content.status = ShopStatus.Opening
    }
    if (data.content.business_hours === undefined || data.content.business_hours.length === 0) {
        data.content.business_hours = [{
            start_time: '09:00',
            end_time: '15:00'
        }]
    }
    if (data.success) {
        return data.content
    } else {
        throw new Error(data.message)
    }
}

/** 将店铺的营业时间转换成字符串 */
export const parseBusinessHour = (hour: IBusinessHour) => {
    return `${hour.start_time} - ${hour.end_time}`
}

/** @deprecated (作废,替代方法searchShops) 根据过滤条件获取店铺列表 */
export const getShops = async (param?: Partial<IShopFilter>): Promise<IPagingResult<IShopView>> => {
    const response = await http.get('shops', param)
    const data = response.data as IJsonResult<IPagingResult<IShopView>>
    if (data.success) {
        return data.content
    } else {
        throw new Error(data.message)
    }
}
/** 通过过滤条件查询店铺 */
export const searchShops = async (option: IShopsFilter): Promise<IPagingResult<IShopView>> => {
    // 构建查询查询
    let param: any = {}
    param.page = option.page
    param.size = option.size
    param.ordering = '-updated_time'
    if (option.industry) {
        param.industry = option.industry
    }
    if (option.rating) {
        let num = parseInt(option.rating)
        param.rating__gte = num // 大于等于该值
        param.rating__lt = num + 1 // 小于该值
    }
    if (option.status === ShopStatus.Opening || option.status === ShopStatus.Suspension) {
        param.status = option.status
    }
    const response = await http.get('shops', param)
    const data = response.data as IJsonResult<IPagingResult<IShopView>>
    return data.content
}

/** 获取店铺评论 */
export const getShopComments: IGetCommentsFunc = async (id: string, page: number, size: number) => {
    const response = await http.get('comment/shops', {
        id,
        page,
        size,
        ordering: '-created_time'
    })
    const data = response.data as IJsonResult<IPagingResult<IShopComment>>
    if (data.success) {
        return data.content
    } else {
        throw new Error(data.message)
    }
}

/** 插入店铺评论 */
export const addShopComment = async ({ targetId, body }: IAddComment): Promise<IShopComment> => {
    const response = await http.post('comment/shops', {
        shop: targetId,
        body
    })
    const data = response.data as IJsonResult<IShopComment>
    if (data.success) {
        return data.content
    } else {
        throw new Error(data.message)
    }
}

