import { PaymentMethods, PaymentMethodsRecord } from '@/enums'
import React from 'react'
import styled from '@emotion/styled'
import SidebarBoxTitle from '@/components/title/SidebarBoxTitle'
import { Box, SxProps, Theme } from '@mui/material'

const StyledBox = styled.div`
  &{
    p{
      margin-left: 24px;
    }
    :first-of-type(p){
      margin-left: 0;
    }
  }  
`

const StyledItem = styled.p`
  font-size: 14px;
  color: #333333;
  line-height: 28px;
`

type Props = {
  data: Array<PaymentMethods>
  sx?: SxProps<Theme>
}

const PaymentMethodBox = ({ data, sx }: Props) => {
    return (
        <Box sx={sx}>
          <SidebarBoxTitle sx={{ paddingY: 2 }}>交付方式</SidebarBoxTitle>
          <Box display="flex" flexWrap="wrap" alignItems='center' gap={2}>
            {data.map((item, index) => (
              <StyledItem key={index}>{PaymentMethodsRecord[item]}</StyledItem>
            ))}
          </Box>
        </Box>
      )
}

export default PaymentMethodBox