import React from 'react'
import styled from '@emotion/styled'
import MessageOutlineIcon from '@/icons/MessageOutlineIcon'

const StyledDiv = styled.div`
    background-color: #60AF1D;
    font-size: 14px;
    color: #FFFFFF;
    padding-top: 14px;
    padding-bottom: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 0.2s linear;
    &:hover{
        background-color: rgba(96,175,29,0.9);
    }
`
const StyledSpan = styled.span`
    padding-left: 10px;
`

type Props = {}

/** 在线联系按钮 */
const ChatButton = (props: Props) => {
    return (
        <StyledDiv>
            <MessageOutlineIcon></MessageOutlineIcon>
            <StyledSpan>在线联系</StyledSpan>
        </StyledDiv>
    )
}

export default ChatButton