import { REACT_APP_API_URL } from "@/config";
import IAccount from "@/interfaces/IAccount";
import IAuthUser from "@/interfaces/IAuthUser";
import IJsonResult from "@/interfaces/IJsonResult";
import ILoginRequest from "@/interfaces/ILoginRequest";
import ILoginResponse from "@/interfaces/ILoginResponse";
import IRegisterRequest from "@/interfaces/IRegisterRequest";
import { http } from "@/utils/http";
import { clearAuthInfo } from "@/utils/storage";
import axios from 'axios'

const login_create = async (email: string, password: string): Promise<ILoginResponse> => {
    const api = `/login`;
    clearAuthInfo()
    let res = await http.post<any, IJsonResult<ILoginResponse>>(api, { email, password })
    return res.data.content
}

/** 登录 */
export const login = async ({ email, password }: ILoginRequest): Promise<IAuthUser> => {
    let loginResponse = await login_create(email, password)
    let account = await getMyAccount(loginResponse.access)
    return {
        accessToken: loginResponse.access,
        refreshToken: loginResponse.refresh,
        username: account.username,
        email: account.email,
        shop_id: account.shop
    }
}

/** 注册 */
export const register = async (account: IRegisterRequest) => {
    clearAuthInfo()
    await http.post('register', account)
}

/** 获取自身账号 */
export const getMyAccount = async (accessToken: string): Promise<IAccount> => {
    const api = `${REACT_APP_API_URL}/account`;
    const apiResponse = await axios.get(api, {
        headers: { Authorization: `Bearer ${accessToken}` },
        validateStatus: function (status) {
            return status < 500;
        }
    })
    if (apiResponse.status >= 200 && apiResponse.status < 300) {
        let data = apiResponse.data as IJsonResult<IAccount>
        if (data.success) {
            return data.content
        } else {
            throw new Error(data.message)
        }
    } else {
        throw new Error(apiResponse.data.detail)
    }
}