import useAuth from '@/hooks/useAuth'
import Paths from '@/layout/Paths'
import { clearAuthInfo } from '@/utils/storage'
import { ListItemText, Menu, MenuItem } from '@mui/material'
import React from 'react'
import { useNavigate } from 'react-router-dom'

interface ILogoutMenuItemProps {
    onClose: () => void
}
const LogoutMenuItem = (props: ILogoutMenuItemProps) => {
    const auth = useAuth()
    const handleClick = () => {
        clearAuthInfo()
        auth.dispatch({ type: "logout" })
        props.onClose()
    }
    return (
        <MenuItem onClick={handleClick}>
            <ListItemText>退出</ListItemText>
        </MenuItem>
    )
}

interface IShopManageMenuItemProps {
    onClose: () => void
}
const ShopManageMenuItem = (props: IShopManageMenuItemProps) => {
    const navigate = useNavigate()
    const handleClick = () => {
        props.onClose()
        navigate('account/shop')
    }
    return (
        <MenuItem onClick={handleClick}>
            <ListItemText>店铺管理</ListItemText>
        </MenuItem>
    )
}

interface ICreateProductMenuItemProps {
    onClose: () => void,
}
const CreateProductMenuItem = (props: ICreateProductMenuItemProps) => {
    const navigate = useNavigate()
    const handleClick = () => {
        props.onClose()
        navigate('account/product/create')
    }
    return (
        <MenuItem onClick={handleClick}>
            <ListItemText>发布商品</ListItemText>
        </MenuItem>
    )
}

interface IProductsOnSaleMenuItemProps {
    onClose: () => void,
}
const ProductsOnSaleMenuItem = (props: IProductsOnSaleMenuItemProps) => {
    const navigate = useNavigate()
    const handleClick = () => {
        props.onClose()
        navigate('account/product')
    }
    return (
        <MenuItem onClick={handleClick}>
            <ListItemText>在售商品</ListItemText>
        </MenuItem>
    )
}

type Props = {
    open: boolean,
    onClose: () => void,
    anchorEl: Element | null
}

const OperateMenu = (props: Props) => {
    return (
        <Menu
            transformOrigin={{ horizontal: 'right', vertical: 'top' }}
            anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
            open={props.open}
            anchorEl={props.anchorEl}
            onClose={props.onClose}
            PaperProps={{
                elevation: 0,
                sx: {
                    overflow: 'visible',
                    filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
                    mt: 1.5,
                    '& .MuiAvatar-root': {
                        width: 32,
                        height: 32,
                        ml: -0.5,
                        mr: 1,
                    },
                    '&:before': {
                        content: '""',
                        display: 'block',
                        position: 'absolute',
                        top: 0,
                        right: 14,
                        width: 10,
                        height: 10,
                        bgcolor: 'background.paper',
                        transform: 'translateY(-50%) rotate(45deg)',
                        zIndex: 0,
                    },
                },
            }}
        >
            <ShopManageMenuItem onClose={props.onClose}></ShopManageMenuItem>
            <CreateProductMenuItem onClose={props.onClose}></CreateProductMenuItem>
            <ProductsOnSaleMenuItem onClose={props.onClose}></ProductsOnSaleMenuItem>
            <LogoutMenuItem onClose={props.onClose}></LogoutMenuItem>
        </Menu>
    )
}

export default OperateMenu