import GuestProtectedRoute from '@/common/GuestProtectedRoute'
import UserProtectedRoute from '@/common/UserProtectedRoute'
import Paths from '@/layout/Paths'
import Home from '@/pages/home/Home'
import Login from '@/pages/Login'
import NotFound from '@/pages/NotFound'
import Register from '@/pages/Register'
import { Route, Routes } from 'react-router-dom'
import AccountLayout from '@/pages/account/AccountLayout'
import Shops from '@/pages/shops/Shops'
import Products from '@/pages/products/Products'
import Product from '@/pages/product/Product'
import Shop from '@/pages/shop/Shop'

type Props = {}

const SiteContent = (props: Props) => {
    return (
        <Routes>
            <Route path="/" element={<Home />} />
            <Route path={Paths.ACCOUNT_LOGIN} element={<GuestProtectedRoute><Login /></GuestProtectedRoute>} />
            <Route path={Paths.ACCOUNT_REGISTER} element={<GuestProtectedRoute><Register /></GuestProtectedRoute>} />
            <Route path={`${Paths.Product}`} element={<Products />} />
            <Route path={`${Paths.Product}/:id`} element={<Product />} />
            <Route path={Paths.Shop} element={<Shops></Shops>}></Route>
            <Route path={`${Paths.Shop}/:id`} element={<Shop></Shop>} />
            {/* 这是一个路由嵌套，子路由的定义在AccountLayout.tsx组件中 */}
            <Route path='account/*' element={<UserProtectedRoute><AccountLayout></AccountLayout></UserProtectedRoute>}></Route>
            <Route path='*' element={<NotFound />} />
        </Routes>
    )
}

export default SiteContent