import { updateShop } from "@/api/shop.api"
import IShop from "@/interfaces/IShop"
import { useMutation, useQueryClient } from "react-query"

/** 更新店铺 */
const useUpdateShop = () => {
    const client = useQueryClient()
    return useMutation<void, Error, IShop>(updateShop, {
        onSuccess: (data, variables) => {
            client.invalidateQueries(['shops'])
            client.invalidateQueries(['shop', { id: variables.id }])
        }
    })
}
export default useUpdateShop