import { useQuery } from "react-query"
import { getShopById } from "@/api/shop.api"
import IShopView from "@/interfaces/IShopView"

/** 通过ID获取店铺 */
const useShop = (id?: string) => {
    return useQuery<IShopView, Error>(
        ["shop", { id }],
        () => getShopById(id!),
        { enabled: !!id } // 如果 id 为 undefined，不执行请求操作
    )
}
export default useShop