import React from 'react'
import styled from '@emotion/styled'
import { Box, Rating } from '@mui/material'
import CertifiedMerchant from '@/components/CertifiedMerchant'
import IShopView from '@/interfaces/IShopView'

const StyledDiv = styled.div`
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: auto auto;
  grid-template-areas: 'image title title' 'image star info';
  column-gap: 1.5rem;
  /* row-gap: 0.5rem; */
`

const StyledImage = styled.img`
  object-fit: cover;
  width: 5.5rem;
  height: 5.5rem;
  grid-area: image;
`

const StyledTitle = styled.p`
  color: #202020;
  font-size: 1.5rem;  
  grid-area: title;
`
const StyledCount = styled.p`
  color: #097EF8;
  font-size: 0.875rem;
`

type Props = {
  shop: IShopView 
}

const Header = ({shop}: Props) => {
  return (
    <StyledDiv>
      <StyledImage src='/images/shop_icon.jpg'></StyledImage>
      <StyledTitle>{shop.name}</StyledTitle>
      <Box gridArea='star' alignItems='center' display='flex'>
        <Rating name="read-only" size='small' value={shop.rating} readOnly />
        <StyledCount>{shop.rating_count}</StyledCount>
        <Box marginLeft='auto'>
          <CertifiedMerchant accountType={shop.user.account_type}></CertifiedMerchant>
        </Box>
      </Box>
    </StyledDiv>
  )
}

export default Header