import React from 'react'
import styled from '@emotion/styled'
import { Box, Grid, Skeleton } from '@mui/material'
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Scrollbar, Autoplay, FreeMode, Thumbs,Mousewheel, Swiper as SwiperClass } from 'swiper';

import 'swiper/css'
import 'swiper/css/bundle'


const StyledImage = styled.img`
    /* object-fit: contain; */
    aspect-ratio: 872 / 510;
    width: 100%;
`
const StyledBox = styled.div`
    &{
        .swiper-button-next, .swiper-button-prev{
            color: white !important;
            opacity: 0.5;
            &:hover{
                opacity: 0.7;
            }
        }
        .swiper-pagination-bullet-active{
            border: 1px solid #F8F9FA;
            background-color: #60AF1D;
        }
    }
`

type Props = {
    images: string[]
}

/** 图片显示 */
const ImageDisplay = ({ images }: Props) => {
    return (
        <React.Fragment>
            <StyledBox>
                <Swiper
                    modules={[Navigation, Pagination, Autoplay, FreeMode, Thumbs,Mousewheel]}
                    loop={true}
                    spaceBetween={10}
                    navigation={true}
                    slidesPerView={1}
                    mousewheel={true}
                    autoplay={{
                        delay: 2000,
                        disableOnInteraction: true,
                    }}
                    pagination={{
                        clickable: true,
                    }}
                    onSwiper={(swiper) => console.log(swiper)}
                    onSlideChange={() => console.log('slide change')}
                >
                    {images.map((photo, index) => (
                        <SwiperSlide key={index}>
                            <StyledImage
                                src={photo}
                                alt="图片"
                                style={{ width: "100%" }}
                            />
                        </SwiperSlide>
                    ))}
                </Swiper>
            </StyledBox>
        </React.Fragment>

    )
}

export const ImageDisplaySkeleton = () => {
    return (
        <Box>
            <Skeleton variant="rectangular" height={100}></Skeleton>
            <Grid container rowSpacing={2} columnSpacing={2} paddingTop={2}>
                <Grid item xs={3}>
                    <Skeleton variant="rectangular" height={50} />
                </Grid>
                <Grid item xs={3}>
                    <Skeleton variant="rectangular" height={50} />
                </Grid>
                <Grid item xs={3}>
                    <Skeleton variant="rectangular" height={50} />
                </Grid>
                <Grid item xs={3}>
                    <Skeleton variant="rectangular" height={50} />
                </Grid>
            </Grid>
        </Box>
    )
}

export default ImageDisplay