import { Box } from '@mui/material'
import React from 'react'
import styled from '@emotion/styled'
import IBaseComment from '@/interfaces/IBaseComment'
import { getAllTimeString } from '@/utils/string.helper'

const StyledName = styled.p`
    color: #333333;
    grid-area: 'name';
    font-size: 1rem;
    font-weight: bold;

`

const StyledTime = styled.p`
    color: #707070;
    grid-area: 'time';
    font-size: 14px;
`

const StyledMessage = styled.p`
    grid-area: 'msg';
`

type Props = {
    data: IBaseComment
}

const Comment = ({ data }: Props) => {
    return (
        <Box
            paddingY={'1.2rem'}
            display='grid'
            gridTemplateRows='auto auto'
            gridTemplateColumns='1fr auto'
            gridTemplateAreas="'name time' 'msg msg'"
            fontSize='1rem'
            sx={{
                borderBottom: 1,
                borderColor: '#EDEDED'
            }}
        >
            <StyledName>{data.user.username}</StyledName>
            <StyledTime>{getAllTimeString(data.created_time)}</StyledTime>
            <StyledMessage>{data.body}</StyledMessage>
        </Box>
    )
}

export default Comment