import { Alert, Box } from '@mui/material'
import React from 'react'
import { Route, Routes } from 'react-router-dom'
import NotFound from '../NotFound'
import Account from './Account'
import CreateProduct from './CreateProduct'
import LeftNav from './LeftNav'
import ModifyProduct from './ModifyProduct'
import Products from './Products'
import Shop from './Shop'

type Props = {}

const AccountLayout = (props: Props) => {
    return (
        <Box sx={{ display: "flex", paddingY: "50px" }}>
            <LeftNav></LeftNav>
            <Box flex={1}>
                <Routes>
                    <Route path="" element={<Account />} />
                    <Route path='shop' element={<Shop></Shop>}></Route>
                    <Route path='product' element={<Products></Products>}></Route>
                    <Route path='product/create' element={<CreateProduct></CreateProduct>}></Route>
                    <Route path='product/update/:id' element={<ModifyProduct></ModifyProduct>}></Route>
                    <Route path='*' element={<NotFound></NotFound>}></Route>
                </Routes>
            </Box>
        </Box>
    )
}

export default AccountLayout