import { searchShops } from "@/api/shop.api"
import IPagingResult from "@/interfaces/IPagingResult"
import IShopsFilter from "@/interfaces/IShopsFilter"
import IShopView from "@/interfaces/IShopView"
import { useQuery } from "react-query"

/** 根据过滤条件搜索店铺 */
const useSearchShops = (filter: IShopsFilter) => {
    return useQuery<IPagingResult<IShopView>, Error>(['shops', filter], () => {
        return searchShops(filter)
    })
}
export default useSearchShops