import { getHotSaleProducts } from "@/api/product.api"
import IPagingResult from "@/interfaces/IPagingResult"
import IProductView from "@/interfaces/IProductView"
import { useQuery } from "react-query"

/** 获取热卖商品(按评论总数、收藏总数排序) */
const useHotSaleProducts = (page: number, size: number, shopId?: string) => {
    return useQuery<IPagingResult<IProductView>, Error>(
        ['hot_sale_products', { page, size, shopId }],
        () => {
            return getHotSaleProducts(page, size, shopId)
        })
}
export default useHotSaleProducts