import IShopView from '@/interfaces/IShopView'
import React from 'react'
import styled from '@emotion/styled'
import { Link } from "react-router-dom"
import { ShopIndustryRecord } from '@/enums'
import { Box, Rating, Skeleton } from '@mui/material'


const StyledBox = styled(Link)`   
    display: block;
    height: 100%;
    transition: box-shadow 0.1s ease-out;
    &:hover{
        &{
            box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),0 2px 4px -1px rgba(0, 0, 0, 0.06);
            .image{
                transform: scale(1.1);
            }
        }
    }
`

const StyledImage = styled.img`
  width: 100%;
  object-fit: cover;
  aspect-ratio: 1.73 / 1;
  transition: .3s ease-in-out;
  transform: scale(1);
`

/** 行业 */
const StyledIndustry = styled.p`
  color: #909090;
  font-size: 0.875rem;
  line-height: 2rem;
`
const StyledFigure = styled.figure`
     width: 100%;
     overflow: hidden;
     aspect-ratio: 1.73 / 1;
`
const StyledTitle = styled.p`
  font-size: 1rem;
  color: #202020;
  font-weight: bold;
  overflow:hidden; 
  text-overflow:ellipsis;
  white-space: nowrap;
`
const StyledStarBox = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #2680EB;
  & .text {
    font-size: 0.875rem;
    line-height: 0.875rem;
    margin-left: 0.5rem;
  }
`

type Props = {
    shop: IShopView
}

/** 店铺卡片 */
const ShopCard = ({ shop }: Props) => {
    let image = shop.images[0]
    if (image == null) {
        image = {
            image: '/images/placeholder.png'
        }
    }
    return (
        <StyledBox to={`/shop/${shop.id}`}>
            <StyledFigure>
                <StyledImage src={image.image} alt="" className='image' />
            </StyledFigure>
            <Box padding={1}>
                <StyledIndustry>{ShopIndustryRecord[shop.industry]}</StyledIndustry>
                <StyledTitle>{shop.name}</StyledTitle>
                <StyledStarBox>
                    <Rating name="read-only" value={shop.rating} readOnly size="small" />
                    <span className='text'>{shop.rating_count}</span>
                </StyledStarBox>
            </Box>
        </StyledBox>
    )
}

export const ShopCardSkeleton = () => {
    return (
        <Box display="flex" flexDirection="column">
            <Skeleton variant="rectangular" height={150} />
            <Skeleton variant="text" />
        </Box>
    )
}

export default ShopCard