import React from 'react'
import styled from '@emotion/styled'
import LicenseIcon from '@/icons/LicenseIcon'
import { AccountType, AccountTypeRecord } from '@/enums'

const StyledBox = styled.div`
    /* width: 6.5rem; */
    height: 2rem;
    border-radius: 1rem;
    background-color: #E0F4FF;
    display: flex;
    align-items: center;
    padding-left: 0.8rem;
    padding-right: 0.8rem;
`

const StyledText = styled.p`
    color: #333333;
    font-size: 0.875rem;
    padding-left: 0.5rem;
`

type Props = {
    accountType: AccountType
}

/** 认证商家(个人)标识 */
const CertifiedMerchant = ({ accountType }: Props) => {
    return (
        <StyledBox>
            <LicenseIcon sx={{ flex: 1 }}></LicenseIcon>
            <StyledText>{AccountTypeRecord[accountType]}账户</StyledText>
        </StyledBox>
    )
}

export default CertifiedMerchant