import { refreshToken } from '@/api/token.api'
import { AuthReducer, getInitAuthState } from '@/auth_store'
import { AuthContextProvider } from '@/contexts/AuthContext'
import { clearAuthInfo, readAuthInfo, writeAuthInfo } from '@/utils/storage'
import React from 'react'

type Props = {
    children: React.ReactNode
}

const Auth = (props: Props) => {
    const [state, dispatch] = React.useReducer(AuthReducer, getInitAuthState())

    const handleToken = async () => {
        try {
            let auth = readAuthInfo()
            if (auth) {
                let token = await refreshToken(auth.refreshToken)
                auth.accessToken = token
                writeAuthInfo(auth)
            }
        } catch (error) {
            console.error(error)
            clearAuthInfo()
            dispatch({ type: "logout" })
        }
    }

    const storageChange = (e: StorageEvent) => {
        let auth = readAuthInfo()
        if (auth == null) {
            dispatch({ type: "logout" })
        }
    }

    React.useEffect(() => {
        const time = 4 * 60 * 1000
        const id = setInterval(() => {
            handleToken()
        }, time)
        handleToken()
        window.addEventListener('storage', storageChange)
        return () => {
            clearInterval(id)
            window.removeEventListener('storage', storageChange)
        }
    }, [])

    return (
        <AuthContextProvider value={{ state, dispatch }}>
            {props.children}
        </AuthContextProvider>
    )
}

export default Auth