import React from 'react'
import ProgressWithLabel from './ProgressWithLabel'

type Props = {}

/** 评分等级 */
const RatingLevels = (props: Props) => {
    return (
        <React.Fragment>
            <ProgressWithLabel label='五星' max={100} value={68}></ProgressWithLabel>
            <ProgressWithLabel label='四星' max={100} value={17}></ProgressWithLabel>
            <ProgressWithLabel label='三星' max={100} value={5}></ProgressWithLabel>
            <ProgressWithLabel label='二星' max={100} value={3}></ProgressWithLabel>
            <ProgressWithLabel label='一星' max={100} value={7}></ProgressWithLabel>
        </React.Fragment>
    )
}

export default RatingLevels