import { REACT_APP_API_URL } from "@/config";
import { PriceNumType } from "@/enums";
import IAddComment from "@/interfaces/IAddComment";
import IGetCommentsFunc from "@/interfaces/IGetCommentsFunc";
import IJsonResult from "@/interfaces/IJsonResult";
import IPagingResult from "@/interfaces/IPagingResult";
import IProduct from "@/interfaces/IProduct";
import IProductComment from "@/interfaces/IProductComment";
import IProductsFilter from "@/interfaces/IProductsFilter";
import IProductView from "@/interfaces/IProductView";
import ISearchProductsRequest from "@/interfaces/ISearchProductsRequest";
import ISearchProductsResponse from "@/interfaces/ISearchProductsResponse";
import IShopProductsRequest from "@/interfaces/IShopProductsRequest";
import TCreateProductRequest from "@/interfaces/TCreateProductRequest";
import { http } from "@/utils/http";
import { isNullOrWhitespace } from "@/utils/string.helper";
import qs from 'qs'
import { getLocationFromAddress } from "./google.api";

/** 调用谷歌API写入商品的位置（经纬度） */
const writeLocation = async (obj: IProduct) => {
    if (isNullOrWhitespace(obj.name)) {
        throw new Error("名称不能为空")
    }
    if (obj.images.length === 0) {
        throw new Error("商品图片不能为空")
    }
    if (obj.price < 0) {
        throw new Error("商品价格不能为负数")
    }
    if (obj.price > 99999) {
        throw new Error("商品价格不能超过99999")
    }
    if (isNullOrWhitespace(obj.address1)) {
        throw new Error("商品地址不能为空")
    }
    const location = await getLocationFromAddress(obj.address1.trim())
    const clone = JSON.parse(JSON.stringify(obj)) as IProduct
    clone.address1 = location.input_address
    clone.address2 = location.formatted_address
    clone.city = location.city
    clone.country = location.country
    clone.latitude = location.latitude
    clone.longitude = location.longitude
    clone.state = location.state
    return clone
}

/** 创建商品 */
export const createProduct = async (obj: TCreateProductRequest) => {
    const clone = await writeLocation(obj as IProduct)
    const url = `${REACT_APP_API_URL}/spus`
    const response = await http.post(url, clone)
    const data = response.data as IJsonResult<any>
    if (!data.success) {
        throw new Error(data.message)
    }
}

/** 获取商品列表 */
export const getProducts = async (params: ISearchProductsRequest): Promise<ISearchProductsResponse> => {
    let defaultConfig: ISearchProductsRequest = {
        page: 1,
        size: 4,
        ordering: '-updated_time'
    }
    let obj = Object.assign(defaultConfig, params)
    const url = `${REACT_APP_API_URL}/spus?` + qs.stringify(obj)
    const response = await http.get(url)
    const data = response.data as IJsonResult<ISearchProductsResponse>
    return data.content
}

/** 通过ID获取商品 */
export const getProductById = async (id: string): Promise<IProductView> => {
    const url = `${REACT_APP_API_URL}/spus/` + id
    const response = await http.get(url)
    const data = response.data as IJsonResult<IProductView>
    return data.content
}

/** 修改商品 */
export const modifyProduct = async (obj: IProduct) => {
    const clone = await writeLocation(obj)
    const url = `${REACT_APP_API_URL}/spus/` + clone.id
    await http.patch(url, clone)
}

/** 删除商品 */
export const removeProduct = async (id: string) => {
    const url = `${REACT_APP_API_URL}/spus/` + id
    await http.delete(url)
}

/**
 * 获取指定店铺的商品
 */
export const getProductsWithShop = async (option: IShopProductsRequest): Promise<IPagingResult<IProductView>> => {
    let defaultOptions = {
        ordering: 'updated_time'
    }
    const response = await http.get('spus', Object.assign(defaultOptions, option))
    const data = response.data as IJsonResult<IPagingResult<IProductView>>
    if (data.success) {
        return data.content
    } else {
        throw new Error(data.message)
    }
}

/**
 * 获取热卖商品(按评论总数、收藏总数排序)
 * @param page 当前页
 * @param size 每页尺寸
 * @param shopId 店铺ID（若设置，则查询指定店铺的产品）
 * @returns 
 */
export const getHotSaleProducts = async (page: number, size: number, shopId?: string): Promise<IPagingResult<IProductView>> => {
    // 服务器端没有相应的API，只是通过简单的排序来提供数据
    const response = await http.get('spus', {
        page,
        size,
        shop: shopId,
        ordering: '-comment_count,-favorite_count'
    })
    const data = response.data as IJsonResult<IPagingResult<IProductView>>
    return data.content
}

/**
 * 获取推荐商品(按商品的更新日期排序)
 */
export const getRecommendProducts = async (page: number, size: number, shopId?: string): Promise<IPagingResult<IProductView>> => {
    // 服务器端没有相应的API，只是通过简单的排序来提供数据
    const response = await http.get('spus', {
        page,
        size,
        shop: shopId,
        ordering: '-updated_time'
    })
    const data = response.data as IJsonResult<IPagingResult<IProductView>>
    return data.content
}

/** 搜索商品 */
export const searchProducts = async (option: IProductsFilter) => {
    // 构建查询查询
    let param: any = {}
    param.page = option.page
    param.size = option.size
    if (option.is_second_hand != null) {
        param.is_second_hand = option.is_second_hand.toString()
    }
    if (option.industry) {
        param.industry = option.industry
    }
    if (option.price) {
        switch (option.price) {
            case PriceNumType.a:
                // 忽略
                break;
            case PriceNumType.b:
                param.price__lt = 25
                break;
            case PriceNumType.c:
                param.price__gte = 25
                param.price__lt = 50
                break;
            case PriceNumType.d:
                param.price__gte = 50
                param.price__lt = 100
                break;
            case PriceNumType.e:
                param.price__gte = 100
                param.price__lt = 200
                break;
            case PriceNumType.f:
                param.price__gte = 200
                break;
            default:
                break;
        }
    }
    const response = await http.get('spus', param)
    const data = response.data as IJsonResult<IPagingResult<IProductView>>
    return data.content
}

/** 插入产品评论 */
export const addProductComment = async ({ targetId, body }: IAddComment): Promise<IProductComment> => {
    const response = await http.post('comment/spus', {
        spu: targetId,
        body
    })
    const data = response.data as IJsonResult<IProductComment>
    if (data.success) {
        return data.content
    } else {
        throw new Error(data.message)
    }
}


/** 获取产品评论 */
export const getProductComments: IGetCommentsFunc = async (id: string, page: number, size: number) => {
    const response = await http.get('comment/spus', {
        id,
        page,
        size,
        ordering: '-created_time'
    })
    const data = response.data as IJsonResult<IPagingResult<IProductComment>>
    if (data.success) {
        return data.content
    } else {
        throw new Error(data.message)
    }
}