import usePage from '@/hooks/usePage'
import { Pagination } from '@mui/material'
import React from 'react'
import { useSearchParams } from 'react-router-dom'

type Props = {
    /** 记录总数 */
    itemCount?: number
    /** 每页记录数 */
    rowsPerPage?: number
    /** 当前页 */
    currentPage?: number
    onChange?: (page: number) => void
}

const Page = ({ itemCount, rowsPerPage, currentPage, onChange }: Props) => {
    const handleChange = (event: React.ChangeEvent<any>, page: number) => {
        if (onChange) {
            onChange(page)
        }
    }
    if (itemCount && rowsPerPage) {
        const count = Math.ceil(itemCount / rowsPerPage)
        return (
            <Pagination
                page={currentPage}
                count={count}
                shape="rounded"
                onChange={handleChange}
            />
        )
    } else {
        return <></>
    }
}

export default Page