import IndustryFilter from '@/filters/IndustryFilter'
import { Box, Grid } from '@mui/material'
import React from 'react'
import Data from './core/Data'
import RatingFilter from './core/RatingFilter'
import StatusFilter from './core/StatusFilter'

type Props = {}

/** 店铺过滤列表页面 */
const Shops = (props: Props) => {

  return (
    <Grid container>
      <Grid item xs={2}>
        <Box paddingY={2}>
          <IndustryFilter></IndustryFilter>
        </Box>
        <Box paddingBottom={2}>
          <RatingFilter></RatingFilter>
        </Box>
        <Box paddingBottom={2}>
          <StatusFilter></StatusFilter>
        </Box>
      </Grid>
      <Grid item xs={10}>
        <Data></Data>
      </Grid>
    </Grid>
  )
}

export default Shops