import { Box, Container } from '@mui/material'
import FeaturedProducts from './core/FeaturedProducts'
import SuggestProducts from './core/SuggestProducts'
import PopularShops from '@/pages/home/core/PopularShops'

const Home = () => {
  return (
    <Box paddingY="40px">
      <FeaturedProducts></FeaturedProducts>
      <PopularShops sx={{ paddingTop: 2 }} />
      <SuggestProducts sx={{paddingTop: 2}}/>
    </Box>
  )
}

export default Home