import React from 'react'
import styled from '@emotion/styled'
import { Box } from '@mui/material'
import SvgIcon from '@mui/material/SvgIcon'

const StyledP = styled.p`
    font-size: 1.875rem;
    color: #333333;
    margin-left: 13px;
`
const Icon = () => {
    return (
        <svg xmlns="http://www.w3.org/2000/svg" width="29.243" height="27" viewBox="0 0 29.243 27">
            <g id="Icon_ionic-md-thumbs-up" data-name="Icon ionic-md-thumbs-up" transform="translate(-3.375 -4.5)">
                <path id="路径_12" data-name="路径 12" d="M13.922,31.5H26.016a2.58,2.58,0,0,0,2.4-1.645l4.015-9.52a2.691,2.691,0,0,0,.183-.984V16.636c0-1.484-1.2-3.136-2.658-3.136H21.579l1.266-5.73.042-.422A2.038,2.038,0,0,0,22.3,5.92L20.883,4.5l-8.859,8.951a2.707,2.707,0,0,0-.773,1.905v13.5A2.651,2.651,0,0,0,13.922,31.5Z" fill="#53aa09" />
                <path id="路径_13" data-name="路径 13" d="M3.375,15.75h4.5V31.5h-4.5Z" fill="#53aa09" />
            </g>
        </svg>

    )
}
const MarkerIcon = () => {
    return (
        <SvgIcon component={Icon}></SvgIcon>
    )
}

/** 精选商品标题 */
const FeaturedProductsTitle = () => {
    return (
        <Box sx={{ display: 'flex', alignItems: 'center', paddingBottom: 2 }}>
            <MarkerIcon></MarkerIcon>
            <StyledP>精选商品</StyledP>
        </Box>
    )
}

export default FeaturedProductsTitle