import { SvgIcon } from '@mui/material'
import React from 'react'
import styled from '@emotion/styled'
import { Link as RouterLink, useNavigate } from "react-router-dom"
import Paths from '@/layout/Paths'
import useAuth from '@/hooks/useAuth'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import OperateMenu from '@/components/head/OperateMenu'

type Props = {}

const StyledRegister = styled(RouterLink)`
    background-color: #53AA09;
    color: white;
    width: 68px;
    height: 36px;
    border-radius: 18px;
    text-align: center;
    line-height: 36px;
    margin-left: auto;
    user-select: none;
    text-decoration: none;
    cursor: pointer;
    transition: background-color linear 0.2s;
    &:hover{
        background-color: rgba(83,170,9,0.8);      
    }
`

const StyledLogin = styled(RouterLink)`
    background-color: white;
    color: #333333;
    width: 68px;
    height: 36px;
    border-radius: 18px;
    text-align: center;
    line-height: 36px;
    margin-left: 20px;
    user-select: none;
    text-decoration: none;
    cursor: pointer;
    transition: background-color linear 0.2s;
    &:hover{
        background-color: rgba(255,255,255,0.8);      
    }
`

const StyledChip = styled.a`
    background-color: white;
    height: 36px;
    line-height: 36px;
    border-radius: 18px;
    text-decoration: none;
    display: flex;
    align-items: center;
    padding: 0px 8px;
    margin-left: 8px;
    &{
        :link{
            color: currentColor;
        }
        :hover{
            background-color: rgba(255,255,255, 0.8);
        }
        :active{
            color: currentColor;
        }
    }

`

const Tools = (props: Props) => {
    const auth = useAuth()
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null)
    const open = Boolean(anchorEl)
    const handleClose = () => {
        setAnchorEl(null)
    }

    const handleClick = (event: React.MouseEvent<HTMLElement>) => {
        event.preventDefault()
        setAnchorEl(event.currentTarget)
    }
    if (auth.state.isLogin) {
        return (
            <React.Fragment>
                <p style={{ marginLeft: "auto" }}>{auth.state.username}</p>
                <StyledChip href='#' onClick={handleClick}>
                    <span>我的</span>
                    <SvgIcon>
                        <KeyboardArrowDownIcon></KeyboardArrowDownIcon>
                    </SvgIcon>
                </StyledChip>
                <OperateMenu anchorEl={anchorEl} open={open} onClose={handleClose}></OperateMenu>
            </React.Fragment>
        )
    } else {
        return (
            <React.Fragment>
                <StyledRegister to={Paths.ACCOUNT_REGISTER}>注册</StyledRegister>
                <StyledLogin to={Paths.ACCOUNT_LOGIN}>登录</StyledLogin>
            </React.Fragment>
        )
    }
}

export default Tools