import { ShopIndustry, ShopStatus } from "@/enums"
import IAddress from "@/interfaces/IAddress"
import IGeometry from "@/interfaces/IGeometry"
import IImage from "@/interfaces/IImage"
import IShop from "@/interfaces/IShop"
import ITimeRange from "@/interfaces/ITimeRange"
import produce from "immer"

export interface IShopState extends IShop {
}

export const defaultShopState: IShopState = {
    name: "",
    country: "",
    state: "",
    city: "",
    address1: "",
    address2: "",
    latitude: 0,
    longitude: 0,
    industry: ShopIndustry.retail,
    status: ShopStatus.Opening,
    phone_num: "",
    images: [],
    detail: "",
    comment_count: 0,
    business_hours: [{
        start_time: '09:00',
        end_time: '15:00'
    }]
}

type ShopAction =
    | { type: 'init', payload: IShop }
    | { type: 'request', payload: boolean }
    | { type: 'reset' }
    | { type: 'changeName', payload: string }
    | { type: 'changeAddress', payload: string }
    | { type: 'changePhoneNum', payload: string }
    | { type: 'changeImages', payload: IImage[] }
    | { type: 'changeStartTime', payload: string }
    | { type: 'changeEndTime', payload: string }
    | { type: 'changeIndustry', payload: ShopIndustry }
    | { type: 'changeStatus', payload: ShopStatus }
    | { type: 'changeDetail', payload: string }
    | { type: 'error', payload: string }
    | { type: 'saving', payload: boolean }


export type ShopDispatch = (action: ShopAction) => void

export const ShopReducer = (state: IShopState, action: ShopAction): IShopState => {
    switch (action.type) {
        case "init":
            return action.payload
        case "reset":
            return defaultShopState
        case "changeName":
            return produce(state, draft => {
                draft.name = action.payload
            })
        case 'changeAddress':
            return produce(state, draft => {
                draft.address1 = action.payload
            })
        case 'changePhoneNum':
            return produce(state, draft => {
                draft.phone_num = action.payload
            })
        case 'changeImages':
            return produce(state, draft => {
                draft.images = action.payload
            })
        case "changeStartTime":
            return produce(state, draft => {
                draft.business_hours[0].start_time = action.payload
            })
        case "changeEndTime":
            return produce(state, draft => {
                draft.business_hours[0].end_time = action.payload
            })
        case "changeIndustry":
            return produce(state, draft => {
                draft.industry = action.payload
            })
        case "changeStatus":
            return produce(state, draft => {
                draft.status = action.payload
            })
        case "changeDetail":
            return produce(state, draft => {
                draft.detail = action.payload
            })
        default:
            throw new Error('Unhandled action type');
    }
}