import React from 'react'
import { useSearchParams } from 'react-router-dom'
import styled from '@emotion/styled'
import { FormControlLabel, Radio, RadioGroup, Typography } from '@mui/material'

type Props = {}

const StyledTitle = styled.p`
    color: #333333;
    font-size: 1.125rem;
    font-weight: bold;    
`

const StyledBox = styled.div`
    & .MuiRadio-root{
        padding-top: 3px;
        padding-bottom: 3px;
        display: flex;
        align-items: center;
    }
    & .MuiTypography-root{
        display: flex;
        align-items: center;
    }
`

enum StateType {
    all = '1',
    new = '2',
    old = '3'
}

const convertValue = (params: URLSearchParams): StateType => {
    if (params.has('is_second_hand')) {
        let value = params.get('is_second_hand')
        if (value === 'true') {
            return StateType.old
        } else {
            return StateType.new
        }
    } else {
        return StateType.all
    }
}

/** 产品新旧过滤器 */
const SecondHandFilter = (props: Props) => {
    const [searchParams, setSearchParams] = useSearchParams()
    const [value, setValue] = React.useState<StateType>(convertValue(searchParams))

    React.useEffect(()=>{
        const newValue = convertValue(searchParams)
        setValue(newValue)
    },[searchParams])

    React.useEffect(() => {
        let newParams = new URLSearchParams(searchParams)
        if (value == StateType.new) {
            newParams.set('is_second_hand', 'false')
        } else if (value == StateType.old) {
            newParams.set('is_second_hand', 'true')
        } else {
            newParams.delete('is_second_hand')
        }
        setSearchParams(newParams)
    }, [value])

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        let newValue = (event.target as HTMLInputElement).value
        setValue(newValue as StateType);
    }

    return (
        <StyledBox>
            <StyledTitle>新旧程度</StyledTitle>
            <RadioGroup value={value} onChange={handleChange}>
                <FormControlLabel
                    value={StateType.all}
                    control={<Radio size="small" />}
                    label={<Typography style={{ fontSize: '1rem' }}>All</Typography>}
                />
                <FormControlLabel
                    value={StateType.new}
                    control={<Radio size="small" />}
                    label={<Typography style={{ fontSize: '1rem' }}>全新</Typography>}
                />
                <FormControlLabel
                    value={StateType.old}
                    control={<Radio size="small" />}
                    label={<Typography style={{ fontSize: '1rem' }}>二手</Typography>}
                />
            </RadioGroup>
        </StyledBox>
    )
}

export default SecondHandFilter