import React from 'react'
import styled from '@emotion/styled'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown'
import { CircularProgress } from '@mui/material'

const StyledLink = styled.a`
    color: #707070;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
`

const StyledSpan = styled.span`
    font-size: 0.875rem;
`

const StyledBox = styled.div`
    height: 30px;
    display: flex;
    justify-content: center;
`

type Props = {
    onClick?: () => void
    hide?: boolean,
    isLoading?: boolean
}

interface IMarkIconProps {
    isLoading?: boolean
}
/** 文字旁小图标 */
const MarkIcon = ({ isLoading }: IMarkIconProps) => {
    if (isLoading) {
        return <CircularProgress sx={{ marginLeft: 1 }} size="1rem" />
    } else {
        return <KeyboardArrowDownIcon fontSize='small'></KeyboardArrowDownIcon>
    }
}

/** 查看更多 */
const SeeMore = ({ isLoading, onClick = undefined, hide = false }: Props) => {
    const handleClick = (e: React.MouseEvent<HTMLAnchorElement>) => {
        e.preventDefault()
        if (onClick) {
            onClick()
        }
    }
    if (hide) {
        return <></>
    } else {
        return (
            <StyledBox>
                <StyledLink href='#' onClick={handleClick}>
                    <StyledSpan>查看更多</StyledSpan>
                    <MarkIcon isLoading={isLoading}></MarkIcon>
                </StyledLink>
            </StyledBox>
        )
    }
}

export default SeeMore