import React from 'react'
import FieldTitle from '@/components/FieldTitle'
import TextField from '@/components/TextField'
import Checkbox from '@/components/Checkbox'
import ImageUploadGroup from '@/components/ImageUploadGroup'
import { Box, FormControlLabel, InputAdornment, Radio } from '@mui/material'
import { PaymentMethods } from '@/enums'
import { IProductFormState, ProductFormAction } from './product.form.store'

type Props = {
    state: IProductFormState
    dispatch: React.Dispatch<ProductFormAction>
    onSubmit: (e: React.FormEvent) => void
    children: React.ReactNode
}

/** 商品表单组件 */
const ProductForm = ({ state, dispatch, onSubmit, children }: Props) => {
    const handleAddressChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = e.target.value.trim()
        dispatch({
            type: "changeAddress",
            payload: value
        })
    }
    const handleCheckboxChange = (event: React.ChangeEvent<HTMLInputElement>, isChecked: boolean) => {
        let value = event.target.value as PaymentMethods
        dispatch({ type: 'changePaymentMethods', payload: { value, isChecked } })
    }
    const handleRadioChange = (event: React.SyntheticEvent<Element, Event>, checked: boolean) => {
        const target = event.target as HTMLInputElement
        const value = target.value
        if (value === "全新") {
            dispatch({ type: 'changeIsSecondHand', payload: !checked })
        } else {
            dispatch({ type: 'changeIsSecondHand', payload: checked })
        }
    }
    const handleSubmit = (e: React.FormEvent) => {
        onSubmit(e)
    }
    return (
        <div style={{ width: "650px" }}>
            <form onSubmit={handleSubmit}>
                <FieldTitle>商品名称</FieldTitle>
                <TextField
                    maxLength={30}
                    fullWidth
                    value={state.name}
                    onChange={e => dispatch({ type: 'changeName', payload: e.target.value })}
                />
                <FieldTitle>商品新旧</FieldTitle>
                <Box>
                    <FormControlLabel
                        value="全新"
                        onChange={handleRadioChange}
                        checked={!state.is_second_hand}
                        control={<Radio />}
                        label="全新" />
                    <FormControlLabel
                        value="二手"
                        onChange={handleRadioChange}
                        checked={state.is_second_hand}
                        control={<Radio />}
                        label="二手" />
                </Box>
                <Box width="312px">
                    <FieldTitle>商品价格</FieldTitle>
                    <TextField
                        maxLength={30}
                        fullWidth
                        type="number"
                        value={state.price.toString()}
                        InputProps={{
                            endAdornment: <InputAdornment position="end">HDO</InputAdornment>
                        }}
                        onChange={e => dispatch({ type: 'changePrice', payload: parseFloat(e.target.value) })}
                    />
                </Box>
                <FieldTitle>商品图片</FieldTitle>
                <ImageUploadGroup
                    disable={state.images.length >= 4}
                    value={state.images}
                    onChange={images => dispatch({ type: 'changeImages', payload: images })}></ImageUploadGroup>
                <FieldTitle>所在地</FieldTitle>
                <TextField maxLength={30} value={state.address1} onChange={handleAddressChange} fullWidth></TextField>
                <FieldTitle>交付方式</FieldTitle>
                <Checkbox
                    label='线下当面交付'
                    value={PaymentMethods['FaceToFace']}
                    onChange={handleCheckboxChange}
                    checked={state.payment_methods.includes(PaymentMethods['FaceToFace'])}
                />
                <Checkbox
                    label='线下同城派送'
                    checked={state.payment_methods.includes(PaymentMethods['InCity'])}
                    value={PaymentMethods['InCity']}
                    onChange={handleCheckboxChange}
                />
                <Checkbox
                    label='线下境内派送'
                    onChange={handleCheckboxChange}
                    value={PaymentMethods.Domestic}
                    checked={state.payment_methods.includes(PaymentMethods.Domestic)}
                />
                <Checkbox
                    label='线下跨境派送'
                    onChange={handleCheckboxChange}
                    value={PaymentMethods.International}
                    checked={state.payment_methods.includes(PaymentMethods.International)}
                />
                <Checkbox
                    label='线上交付'
                    onChange={handleCheckboxChange}
                    value={PaymentMethods.Online}
                    checked={state.payment_methods.includes(PaymentMethods.Online)}
                />
                <FieldTitle>商品详情</FieldTitle>
                <TextField
                    multiline
                    rows={4}
                    fullWidth
                    value={state.detail}
                    onChange={e => dispatch({ type: 'changeDetail', payload: e.target.value })}
                />
                {children}
            </form>
        </div>
    )
}

export default ProductForm