import React from 'react'
import styled from '@emotion/styled'

const StyledTitle = styled.p`
    color: #333333;
    font-size: 1.375rem;
    font-weight: bold;
    padding-top: 1rem;
    padding-bottom: 1rem;
`

const StyledContent = styled.p`
    color: #333333;
    font-size: 1rem;
`

type Props = {
    text: string
}

/** 详情组件 */
function Detail({ text }: Props) {
    return (
        <div>
            <StyledTitle>详情</StyledTitle>
            <StyledContent>{text}</StyledContent>
        </div>
    )
}

export default Detail