import React from 'react'
import styled from '@emotion/styled'
import { Box } from '@mui/system'
import { Link } from 'react-router-dom'
import Paths from '@/layout/Paths'

type Props = {}

const StyledTitle = styled.p`
    font-size: 1.875rem;
    color: #333333;
    line-height: 2rem;   
    padding-bottom: 25px;
`

const StyledLink = styled(Link)`
    text-decoration: none;
    padding-bottom: 25px;
`

const LeftNav = (props: Props) => {
    return (
        <div style={{ paddingLeft: "60px", paddingRight: "150px" }}>
            <StyledTitle>我的账户</StyledTitle>
            <Box display="flex" flexDirection="column" alignItems="end">
                <StyledLink to="">账户设置</StyledLink>
                <StyledLink to="shop">店铺设置</StyledLink>
                <StyledLink to="product/create">发布商品</StyledLink>
                <StyledLink to="product">在售商品</StyledLink>
            </Box>
        </div>
    )
}

export default LeftNav