import { Box, Rating } from '@mui/material'
import React from 'react'
import styled from '@emotion/styled'

const StyledSpan = styled.span`
    font-size: 1.75rem;
    font-weight: bold;
`

const StyledCommentNum = styled.p`
    color: #333333;
    font-size: 14px;
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-weight: bold;
`

type Props = {}

/** 评分 */
const Ratings = (props: Props) => {
    return (
        <React.Fragment>
            <Box display='flex' alignItems='center' gap={1}>
                <Rating name="size-large" defaultValue={4.1} size="large" />
                <StyledSpan>4.1</StyledSpan>
            </Box>
            <StyledCommentNum>2,345 评论</StyledCommentNum>
        </React.Fragment>
    )
}

export default Ratings