import { PriceNumType } from '@/enums'
import React from 'react'
import { useSearchParams } from 'react-router-dom'
import styled from '@emotion/styled'
import { FormControlLabel, Radio, RadioGroup, Typography } from '@mui/material'

const StyledTitle = styled.p`
    color: #333333;
    font-size: 1.125rem;
    font-weight: bold;    
`

const StyledBox = styled.div`
    & .MuiRadio-root{
        padding-top: 3px;
        padding-bottom: 3px;
        display: flex;
        align-items: center;
    }
    & .MuiTypography-root{
        display: flex;
        align-items: center;
    }
`

type Props = {}

const convertValue = (params: URLSearchParams): PriceNumType => {
    if (params.has('price')) {
        let value = params.get('price')
        return value as PriceNumType
    } else {
        return PriceNumType.a
    }
}

/** 商品价格过滤器 */
const PriceFilter = (props: Props) => {
    const [searchParams, setSearchParams] = useSearchParams()
    const [value, setValue] = React.useState<PriceNumType>(convertValue(searchParams))

    React.useEffect(() => {
        const newValue = convertValue(searchParams)
        setValue(newValue)
    }, [searchParams])

    React.useEffect(() => {
        let newParams = new URLSearchParams(searchParams)
        switch (value) {
            case PriceNumType.a:
                newParams.delete('price')
                break;
            case PriceNumType.b:
                newParams.set('price', 'b')
                break;
            case PriceNumType.c:
                newParams.set('price', 'c')
                break;
            case PriceNumType.d:
                newParams.set('price', 'd')
                break;
            case PriceNumType.e:
                newParams.set('price', 'e')
                break;
            case PriceNumType.f:
                newParams.set('price', 'f')
                break;
            default:
                newParams.delete('price')
                break;
        }
        setSearchParams(newParams)
    }, [value])

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        let newValue = (event.target as HTMLInputElement).value
        setValue(newValue as PriceNumType)
    }
    return (
        <StyledBox>
            <StyledTitle>价格</StyledTitle>
            <RadioGroup value={value} onChange={handleChange}>
                <FormControlLabel
                    value={PriceNumType.a}
                    control={<Radio size="small" />}
                    label={<Typography style={{ fontSize: '1rem' }}>All</Typography>}
                />
                <FormControlLabel
                    value={PriceNumType.b}
                    control={<Radio size="small" />}
                    label={<Typography style={{ fontSize: '1rem' }}>$25 以下</Typography>}
                />
                <FormControlLabel
                    value={PriceNumType.c}
                    control={<Radio size="small" />}
                    label={<Typography style={{ fontSize: '1rem' }}>$25 至 $50</Typography>}
                />
                <FormControlLabel
                    value={PriceNumType.d}
                    control={<Radio size="small" />}
                    label={<Typography style={{ fontSize: '1rem' }}>$50 至 $100</Typography>}
                />
                <FormControlLabel
                    value={PriceNumType.e}
                    control={<Radio size="small" />}
                    label={<Typography style={{ fontSize: '1rem' }}>$100 至 $200 </Typography>}
                />
                <FormControlLabel
                    value={PriceNumType.f}
                    control={<Radio size="small" />}
                    label={<Typography style={{ fontSize: '1rem' }}>$200 及以上</Typography>}
                />
            </RadioGroup>
        </StyledBox>
    )
}

export default PriceFilter