import { getProductsWithShop } from "@/api/product.api"
import useAuth from "@/hooks/useAuth"
import IPagingResult from "@/interfaces/IPagingResult"
import IProductView from "@/interfaces/IProductView"
import { useQuery } from "react-query"
import useDefaultShop from "../shop/useDefaultShop"

/** 获取本店在售商品 */
const useSaleProducts = (page: number) => {
    const auth = useAuth()
    const shopId = auth.state.shopId
    return useQuery<IPagingResult<IProductView>, Error>([
        'account/product',
        { page, shopId }
    ], () => {
        return getProductsWithShop({
            page: page,
            shop: shopId!,
            size: 24
        })
    }, {
        enabled: !!shopId
    })
}
export default useSaleProducts