import React from 'react'
import styled from '@emotion/styled'
import UploadButton from '@/components/UploadButton';
import ImageBox from '@/components/ImageBox';
import IImage from '@/interfaces/IImage';

type Props = {
    value: IImage[],
    onChange: (images: IImage[]) => void
    disable: boolean
}

const StyledDiv = styled.div`
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
`

const ImageUploadGroup = (props: Props) => {
    const handleUpload = (image: IImage) => {
        const newList = [...props.value]
        newList.push(image)
        props.onChange(newList)
    }
    const handleRmove = (url?: string) => {
        let newList = props.value.filter(item => item.image !== url)
        props.onChange(newList)
    }
    return (
        <StyledDiv>
            <UploadButton onUpload={handleUpload} disable={props.disable}></UploadButton>
            {props.value.map((item, index) => (
                <ImageBox key={index} url={item.image} onRmove={handleRmove}></ImageBox>
            ))}
        </StyledDiv>
    )
}

export default ImageUploadGroup