import { searchProducts } from "@/api/product.api"
import IPagingResult from "@/interfaces/IPagingResult"
import IProductsFilter from "@/interfaces/IProductsFilter"
import IProductView from "@/interfaces/IProductView"
import { useQuery } from "react-query"

/** 搜索商品 */
const useSearchProducts = (filter: IProductsFilter) => {
    return useQuery<IPagingResult<IProductView>, Error>(['products', filter], () => {
        return searchProducts(filter)
    })
}
export default useSearchProducts