import { Box, Container } from '@mui/material'
import React from 'react'
import styled from '@emotion/styled'

type Props = {}

const StyledDiv = styled.div`
    background-color: #F2F2F2;
    padding: 34px 0;
    margin-top: auto;
    & .title{
        font-size: 20px;
    }
    & ul{
        display: flex;
        list-style: none;
        align-items: center;
        justify-content: space-between;
        width: 325px;
        padding: 20px 0 39px 0;
        &>li>a{
            font-size: 14px;
            line-height: 20px;
            text-decoration: none;
            color: #333333;
        }
    }
    & .copyright{
        font-size: 14px;
        color: #666666;
    }
`

const Footer = (props: Props) => {
    return (
        <StyledDiv>
            <Container sx={{ color: "#333333" }}>
                <p className='title'>HPay消费指南</p>
                <ul>
                    <li><a href="#">首页</a></li>
                    <li><a href="#">关于</a></li>
                    <li><a href="#">联系我们</a></li>
                    <li><a href="#">帮助</a></li>
                </ul>
                <p className='copyright'>© 2022 HPay Consumer Guide. All Rights Reserved.</p>
            </Container>
        </StyledDiv>
    )
}

export default Footer