import React from 'react'
import styled from '@emotion/styled'
import { Box } from '@mui/material'
import SvgIcon from '@mui/material/SvgIcon'

const StyledP = styled.p`
    font-size: 1.875rem;
    color: #333333;
    margin-left: 13px;
`
const Icon = () => {
    return (
        <svg xmlns="http://www.w3.org/2000/svg" width="30" height="40" viewBox="0 0 30 40">
            <path id="Icon_awesome-map-marker-alt" data-name="Icon awesome-map-marker-alt" d="M13.458,39.193C2.107,22.737,0,21.048,0,15a15,15,0,0,1,30,0c0,6.048-2.107,7.737-13.458,24.193a1.876,1.876,0,0,1-3.083,0ZM15,21.25A6.25,6.25,0,1,0,8.75,15,6.25,6.25,0,0,0,15,21.25Z" fill="#53aa09" />
        </svg>
    )
}
const MapMarkerIcon = () => {
    return (
        <SvgIcon component={Icon}></SvgIcon>
    )
}

/** 热门商家（店铺）标题 */
const PopularShopsTitle = () => {
    return (
        <Box sx={{ display: 'flex', alignItems: 'center', paddingBottom: 2 }}>
            <MapMarkerIcon></MapMarkerIcon>
            <StyledP>热门商家</StyledP>
        </Box>
    )
}

export default PopularShopsTitle