import React from 'react'
import { Alert, Box, Grid, SxProps, Theme } from '@mui/material'
import ProductCard from '@/components/ProductCard'
import SeeMore from '@/components/SeeMore'
import IPagingResult from '@/interfaces/IPagingResult'
import IProductView from '@/interfaces/IProductView'
import useScrollSuggestProducts from '@/query/product/useScrollSuggestProducts'
import LoadingSkeleton from './LoadingSkeleton'
import SuggestProductsTitle from './SuggestProductsTitle'

type Props = {
    sx?: SxProps<Theme>
}

const SIZE = 4

interface IRenderProps {
    data?: IPagingResult<IProductView>[]
}
const Render = ({ data }: IRenderProps) => {
    if (data) {
        return (
            <React.Fragment>
                {data.map(page => {
                    return page.results.map(item => (
                        <Grid item xs={3} flex={1} key={item.id}>
                            <ProductCard data={item}></ProductCard>
                        </Grid>
                    ))
                })}
            </React.Fragment>
        )
    } else {
        return <></>
    }
}

/** 推荐商品 */
const SuggestProducts = ({ sx }: Props) => {
    const query = useScrollSuggestProducts()
    if (query.isLoading) {
        return <LoadingSkeleton></LoadingSkeleton>
    } else if (query.isError) {
        return <Alert severity="error">{(query.error as Error).message}</Alert>
    } else {
        return (
            <React.Fragment>
                <Box sx={sx}>
                    <SuggestProductsTitle></SuggestProductsTitle>
                    <Grid container spacing={2}>
                        <Render data={query.data?.pages}></Render>
                    </Grid>
                    <SeeMore onClick={() => query.fetchNextPage()} hide={!query.hasNextPage} isLoading={query.isFetchingNextPage}></SeeMore>
                </Box>
            </React.Fragment>
        )
    }
}

export default SuggestProducts