import React from 'react'
import styled from '@emotion/styled'
import { Link } from 'react-router-dom'
import Paths from '@/layout/Paths'
import { Box, CircularProgress, Rating, Skeleton } from '@mui/material'
import useRemoveProduct from '@/query/product/useRemoveProduct'
import IProductView from '@/interfaces/IProductView'

const StyledFigure = styled.figure`
     width: 100%;
     overflow: hidden;
     aspect-ratio: 1.73 / 1;
`

const StyledImg = styled.img`
   width: 100%;
  object-fit: cover;
  aspect-ratio: 1.73 / 1;
  transition: .3s ease-in-out;
  transform: scale(1);
`

const StyledBox = styled(Link)`   
    display: block;
    height: 100%;
    transition: box-shadow 0.1s ease-out;
    &:hover{
        &{
            box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),0 2px 4px -1px rgba(0, 0, 0, 0.06);
            .image{
                transform: scale(1.1);
            }
        }
    }
`

const StyledTitle = styled.p`
    color: #202020;
    font-size: 1rem;
    font-weight: bold;
    word-wrap: break-word;
`


const StyledP = styled.p`
    display: flex;
    align-items: flex-end;    
`

const StyledUnit = styled.span`
    font-size: 1rem;
    color: #202020;
    margin-right: 0.3rem;
`

const StyledPrice = styled.span`
    font-size: 1.375rem;
    color: #53AA09;
`

const StyledBar = styled.div`
    display: flex;
    margin-top: 0.5rem;
`

const StyledButton = styled(Link)`
    font-size: 0.875rem;
    color: #707070;
    text-decoration: none;
    margin-right: 1rem;
    transition: color 0.2s linear;
    &:hover{
        color: black;
        text-decoration: underline;
    }
`
const StyledStarBox = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #2680EB;
  & .text {
    font-size: 0.875rem;
    line-height: 0.875rem;
    margin-left: 0.5rem;
  }
`

type Props = {
    data: IProductView,
    canEdit?: boolean
}

/** 商品卡片 */
const ProductCard = ({ data, canEdit }: Props) => {
    const mutation = useRemoveProduct()
    const handleDelete = () => {
        mutation.mutate(data.id)
    }
    let image = '/images/placeholder.png'
    if (data.images && data.images.length > 0) {
        image = data.images[0].image
    }
    return (
        <StyledBox to={`/${Paths.Product}/${String(data.id)}`}>
            <StyledFigure>
                <StyledImg src={image} className='image' />
            </StyledFigure>
            <Box padding={1}>
                <StyledTitle>{data.name}</StyledTitle>
                <StyledStarBox>
                    <Rating name="read-only" value={data.rating} readOnly size="small" />
                    <span className='text'>{data.rating_count}</span>
                </StyledStarBox>
                <StyledP>
                    <StyledUnit>HDO</StyledUnit>
                    <StyledPrice>{data.price}</StyledPrice>
                </StyledP>
                {
                    canEdit && (
                        <StyledBar>
                            <StyledButton to={'/account/product/update/' + data.id}>编辑</StyledButton>
                            <StyledButton to={'#'} onClick={handleDelete}>
                                <span>删除</span>
                                {mutation.isLoading && <CircularProgress sx={{ marginLeft: 1 }} size="1rem" />}
                            </StyledButton>
                        </StyledBar>
                    )
                }
            </Box>

        </StyledBox >
    )
}

export const ProductCardSkeleton = () => {
    return (
        <Box display="flex" flexDirection="column">
            <Skeleton variant="rectangular" height={150} />
            <Skeleton variant="text" />
        </Box>
    )
}

export default ProductCard